import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Card } from 'primereact/card';
import { Button } from 'primereact/button';
import { ListBox } from 'primereact/listbox';
import { ProgressSpinner } from 'primereact/progressspinner';
import './styles.scss';

const CardListBox = ({
  // eslint-disable-next-line react/prop-types
  title, data, field, watchDog,
}) => {
  CardListBox.propTypes = {
    title: PropTypes.string.isRequired,
    data: PropTypes.arrayOf(Object).isRequired,
    field: PropTypes.string.isRequired,
  };

  const [values, setValues] = useState(data);
  useEffect(() => {
    setValues(data);
  }, [setValues, data]);

  const listTemplate = (item) => {
    if (item && (title === 'Logiciels')) {
      return (
        <div className="template-container">
          <div className="info-container">
            <div className="name-container">
              <p>{item[field]}</p>
            </div>
            <div className={item.status === 'UP' ? 'green-round' : 'red-round'} />
          </div>
          <Button
            icon="pi pi-undo"
            className="p-button-rounded p-button-primary"
            // onClick={() => restartDevice(item)}
            disabled
          />
        </div>
      );
    } if (item) {
      return (
        <div className="template-container">
          <div className="info-container">
            <div className="name-container">
              <p>{item[field]}</p>
            </div>
            {
              watchDog ? (
                <div className={item.isAlive ? 'green-round' : 'red-round'} />
              )
                : (
                  <div className={item.status === 'UP' ? 'green-round' : 'red-round'} />
                )
            }
          </div>
        </div>
      );
    }
    return null;
  };

  const renderListBox = () => {
    if (!values) {
      return <ProgressSpinner />;
    }
    if (values.length === 0) {
      return <p className="no-data">Aucun service trouvé</p>;
    }
    if (values.length > 0) {
      return <ListBox options={values} itemTemplate={listTemplate} />;
    }
    return null;
  };

  return (
    <div className="CardListBox">
      <Card title={title} className="Card">
        {renderListBox()}
      </Card>
    </div>
  );
};

export default CardListBox;
