import AbstractService from './abstractService';

export default class BroadcastConfigurationService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/broadcasts-configurations'));
  }

  get(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const broadcastsConfigurations = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              broadcastsConfigurations.push(json[index]);
            }
          } else {
            broadcastsConfigurations.push(json);
          }
          resolve(broadcastsConfigurations);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  create(filters, broadcastConfiguration) {
    return new Promise((resolve, reject) => [
      super.create(filters, broadcastConfiguration)
        .then((message) => {
          resolve(message);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  update(filters, identifier, broadcastConfiguration) {
    return new Promise((resolve, reject) => [
      super.update(filters, identifier, broadcastConfiguration)
        .then((message) => {
          resolve(message);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }
}
