/**
 * Class that represents an event log.
 * @since 0.10.2
 * @author METIVIER Nicolas
 */
class EventLog {
  constructor() {
    this.date = null;
    this.level = null;
    this.entity = null;
    this.message = null;
  }

  static compare(previous, next) {
    if (previous.date < next.date) {
      return -1;
    }
    if (previous.date > next.date) {
      return 1;
    }
    return 0;
  }

  /**
   * Function to convert a JSON to Bulletin object.
   * @param {JSON} json Bulletin as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.date !== undefined) {
        if (json.level !== undefined) {
          if (json.entity !== undefined) {
            if (json.message !== undefined) {
              const eventLog = new EventLog();
              eventLog.date = new Date(json.date);
              eventLog.level = json.level;
              eventLog.entity = json.entity;
              eventLog.message = json.message;
              return eventLog;
            }
          }
        }
      }
    }
    return null;
  }
}

export default EventLog;
