/* eslint-disable react/prop-types */
/* eslint-disable react/destructuring-assignment */
import React, { useEffect, useState } from 'react';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import UtcDate from '../../../entities/date';

const AnomalyTable = (props) => {
  const [anomalies, setAnomalies] = useState(props.alerts);

  useEffect(() => {
    setAnomalies(props.alerts);
  }, [props.alerts]);

  const bodyDate = (alert) => {
    if (alert) {
      return (
        <>
          {
            new UtcDate(new Date(alert.date)).format(process.env.REACT_APP_DATE_FORMAT)
          }
        </>
      );
    }
    return null;
  };

  return (
    <div>
      <div className="card">
        <DataTable
          value={anomalies}
          paginator
          header={props.header()}
          rows={10}
          rowsPerPageOptions={[10, 30, 60]}
          paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
          emptyMessage="Il n'y a aucune alerte."
        >
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyDate} header="Date" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} filter filterPlaceholder="Rechercher par type" field="type" header="Type" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} field="detail" header="Description" />
        </DataTable>
      </div>
    </div>
  );
};

export default AnomalyTable;
