/* eslint-disable react/destructuring-assignment */
/* eslint-disable max-len */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-lonely-if */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable react/prop-types */
/* eslint-disable-next-line react/jsx-one-expression-per-line */
import React, {
  useState, useRef, useEffect,
} from 'react';
import { Column } from 'primereact/column';
import { Badge } from 'primereact/badge';
import { DataTable } from 'primereact/datatable';
import { Dialog } from 'primereact/dialog';
import { Toast } from 'primereact/toast';
import { connect } from 'react-redux';
import { Tag } from 'primereact/tag';
import { Tooltip } from 'primereact/tooltip';
import { useCookies } from 'react-cookie';
import { v4 as uuidv4 } from 'uuid';
import Flag from 'react-world-flags';
import { setError } from '../../../store/error/errorAction';
import WebsocketService from '../../../services/websocketService';
import WebSocketClientEventListener from '../../../tools/websocket/websocket-eventlistener';
import Bulletin from '../../../entities/bulletin';

import './styles.scss';
import BulletinService from '../../../services/bulletinService';
import parseCountry from '../../../tools/country-code-parser';
import UtcDate from '../../../entities/date';

const BulletinTable = (props) => {
  class BulletinEventListener extends WebSocketClientEventListener {
    onOpen(event) {
      console.info('Websocket client is now connected.', event);
    }

    onError(event) {
      console.error(event);
    }

    onMessage(event) {
      const json = WebSocketClientEventListener.getJson(event.data);

      if (json) {
        const receivedBulletin = Bulletin.parse(json);
        if (receivedBulletin !== null) {
          const list = [...bulletins];
          const bulletinIndex = list.findIndex((bulletin) => bulletin.name === receivedBulletin.name);
          // If bulletin not exist, we add it.
          if (bulletinIndex === -1) {
            list.unshift(receivedBulletin);
            if (list.length > 60) {
              list.pop();
            }
          } else {
            if (props.refreshData) {
              list[bulletinIndex] = receivedBulletin;
              if (receivedBulletin.status === 'READY') {
                let isNewBulletin = false;
                if (receivedBulletin.translations != null) {
                  for (let translationIndex = 0; translationIndex < receivedBulletin.translations.length; translationIndex += 1) {
                    const { broadcasts } = receivedBulletin.translations[translationIndex];
                    if (broadcasts == null || (broadcasts.length === 0)) {
                      isNewBulletin = true;
                    }
                  }
                }
                if (isNewBulletin) {
                  props.setDoRequest(true);
                }
              } else if (receivedBulletin.status === 'ERROR') {
                props.setDoRequest(true);
              }
            }
          }
          setBulletins(list);
        }
      }
    }

    onClose(event) {
      console.info(this, 'Websocket clientis now disconnected.', event);
    }
  }

  const [cookies] = useCookies();
  const [bulletins, setBulletins] = useState([]);
  // const [translations, setTranslations] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [decodedTextToDisplay, setdecodedTextToDisplay] = useState(null);
  const [audioUrl, setAudioUrl] = useState(null);
  const toast = useRef(null);

  // Optionnals
  // const [broadcastingBulletin, setBroadcastingBulletin] = useState(null);

  const [expandedRows, setExpandedRows] = useState(null);
  const audio = document.getElementsByClassName('audio-element')[0];
  WebsocketService.getInstance().setEventListener(new BulletinEventListener());

  useEffect(() => {
    if (props.values) {
      setBulletins(props.values);
      console.log(props.values);
    }
  }, [props]);

  const showTextModal = (e, decodedText) => {
    setShowModal(true);
    setdecodedTextToDisplay(decodedText);
  };

  const bodyOpenSettings = (translation) => (
    <i
      onClick={(e) => showTextModal(e, translation.decodedText)}
      className="pi pi-info-circle"
    />
  );

  const bodyLanguage = (bulletin) => {
    if (bulletin && bulletin.language) {
      return (
        <Flag
          code={parseCountry(bulletin.language)}
          height={20}
        />
      );
    }
    return <span>Langue en cours de récupération</span>;
  };

  const bodyDate = (bulletin) => {
    if (bulletin) {
      return (
        <>
          {
            new UtcDate(new Date(bulletin.date)).format(process.env.REACT_APP_DATE_FORMAT)
          }
        </>
      );
    }
    return null;
  };

  const bodyBulletinStatus = (bulletin) => {
    if (bulletin) {
      if (bulletin.status === 'ERROR' || bulletin.error) {
        const ID = 'badge-'.concat(uuidv4().toString().replace('-', ''));
        return (
          <>
            <Tooltip target={'.'.concat(ID)} content={bulletin.error} position="bottom" />
            <Badge id={ID} value={bulletin.status} severity="danger" className={`customer-badge status-${bulletin.status} ${ID}`} />
          </>
        );
      }
      if (bulletin.status === 'READY') {
        if (!bulletin.isOutdated) {
          return (<Badge value="Valide" severity="success" />);
        }
        return (<Badge className="background-grey" value="Obsolète" />);
      }
      return (
        <Badge value={bulletin.status} severity="warning" /* className={`customer-badge status-${bulletin.status}`} */ />
      );
    }
    return null;
  };

  const bodyTranslationStatus = (translation) => {
    const ID = 'badge-'.concat(uuidv4().toString().replace('-', ''));
    if (translation) {
      if (translation.status === 'ERROR' || translation.error) {
        return (
          <>
            <Tooltip target={'.'.concat(ID)} content={translation.error} position="bottom" />
            <Badge id={ID} value={translation.status} severity="danger" />
          </>
        );
      }
      if (translation.status === 'PROCESSING') {
        return (
          <Badge value={translation.status} severity="warning" />
        );
      }

      if (translation.isAudioFileExists) {
        return (
          <Badge value="Disponible" severity="success" />
        );
      }
      return (
        <>
          <Tooltip target={'.'.concat(ID)} content="Audio file not exists." position="bottom" />
          <Badge id={ID} value="ERROR" severity="danger" className={`status-${translation.status} ${ID}`} />
        </>
      );
    }
    return null;
  };

  const playPause = async (e, translation) => {
    try {
      if (translation.status !== 'READY') {
        toast.current.show({
          severity: 'error', summary: 'Erreur', detail: "Message audio indisponible ( voir les paramètres de l'audio )", life: 3000,
        });
      } else {
        const newBulletinsList = [...bulletins];
        const { audioBulletinIdentifier } = audio;

        // Information sur la traduction sur laquel on vient de cliquer
        const bulletinIndex = newBulletinsList.findIndex((item) => item.name === translation.bulletinName);
        const translationIndex = newBulletinsList[bulletinIndex].translations.findIndex((item) => item.identifier === translation.identifier);

        // Si on clique sur le meme qu'au coup d'avant
        if (audioBulletinIdentifier && audioBulletinIdentifier === translation.identifier) {
          if (translation.isPlaying) {
            newBulletinsList[bulletinIndex].translations[translationIndex].isPlaying = false;
            audio.pause();
          } else {
            newBulletinsList[bulletinIndex].translations[translationIndex].isPlaying = true;
            audio.play();
          }
        } else {
        // 1 - Les isPlaying qui sont a 'true' on les passe a 'false'
          newBulletinsList.forEach((bulletin) => {
            bulletin.translations.forEach((_translation) => {
              if (_translation.isPlaying) {
                _translation.isPlaying = false;
              }
            });
          });
          // 2 - On refait notre requête à l'api
          const bulletinService = new BulletinService();
          const response = await bulletinService.getAudioBulletin(
            cookies.jwt,
            translation.language,
            translation.bulletinIdentifier,
          );
          if (response.status === 200) {
            audio.audioBulletinIdentifier = translation.identifier;
            newBulletinsList[bulletinIndex].translations[translationIndex].isPlaying = true;
            const blobUrl = URL.createObjectURL(response.blob);
            // 3 - On SET notre 'blobUrl'
            setAudioUrl(blobUrl);
            // 4 - On lance l'audio
            audio.play();
          } else {
            console.error("Erreur lors du téléchargement de l'audio");
          }
        }
        setBulletins(newBulletinsList);
      }
    } catch (err) {
      props.setErrorInState(err.response ? err.response.status : err.status || 500);
    }
  };

  const bodyTranslationsLanguage = (bulletin) => {
    if (bulletin) {
      return bulletin.translations.map((translation) => {
        if (translation) {
          const ID = 'badge-'.concat(uuidv4().toString().replace('-', ''));
          if (translation.status === 'ERROR' || translation.error) {
            return (
              <>
                <Tooltip target={'.'.concat(ID)} content={translation.error} position="bottom" />
                <Badge id={ID} value={translation.language} severity="danger" className={`status-${translation.status.toLowerCase()} ${ID}`} />
              </>
            );
          }
          if (translation.status === 'PROCESSING') {
            return (
              <Badge value={translation.language} severity="warning" />
            );
          }
          if (translation.isAudioFileExists && translation.status === 'READY') {
            return (
              <Badge value={translation.language} severity="success" />
            );
          }
          return (
            <>
              <Tooltip target={'.'.concat(ID)} content="Audio file not exists." position="bottom" />
              <Badge id={ID} value={translation.language} severity="danger" className={`${ID}`} />
            </>
          );
        }
        return null;
      });
    }
    return null;
  };

  const bodyPlayPause = (translation) => {
    if (translation) {
      if (translation.isAudioFileExists) {
        if (!translation.isPlaying) {
          return (
            <i
              onClick={(e) => playPause(e, translation)}
              className={`pi pi-play ${translation.status === 'ERREUR' ? 'play-error' : ''}`}
            />
          );
        }
        return (
          <i
            onClick={(e) => playPause(e, translation)}
            className="pi pi-pause"
          />
        );
      }
    }
    return null;
  };

  const bodyBulletinBrodcasting = (bulletin) => {
    if (bulletin) {
      let isBroadcasting = false;
      for (let translationIndex = 0; translationIndex < bulletin.translations.length; translationIndex += 1) {
        for (let broadcastIndex = 0; broadcastIndex < bulletin.translations[translationIndex].broadcasts.length; broadcastIndex += 1) {
          if (bulletin.translations[translationIndex].broadcasts[broadcastIndex].isBroadcasting) {
            isBroadcasting = true;
            break;
          }
        }
        if (isBroadcasting) {
          break;
        }
      }
      if (isBroadcasting) {
        return <i className="pi pi-wifi" style={{ fontSize: '2em', color: 'rgba(0,200,0,1)' }} />;
      }
      return <i className="pi pi-wifi" style={{ fontSize: '1em', color: 'rgba(200,200,200,1)' }} />;
    }
    return null;
  };

  const bodyTranslationBrodcasting = (translation) => {
    if (translation.broadcasts) {
      return translation.broadcasts.map((broadcast) => {
        if (broadcast.isBroadcasting) {
          let icon = null;
          switch (broadcast.broadcastType) {
            case 'RTC':
              icon = 'pi-phone';
              break;
            case 'VHF':
              icon = 'pi-wifi';
              break;
            default:
              break;
          }
          return <Tag value={broadcast.broadcastType} icon={String('pi ').concat(icon)} />;
        }
        return null;
      });
    }
    return null;
  };

  const bodyProtocol = (bulletin) => {
    if (bulletin) {
      return (<Tag style={{ fontSize: '1rem', background: '#3f51b5' }} value={bulletin.report_type} />);
    }
    return null;
  };

  const bodyAudioFormat = (translation) => (
    <Tag severity="info" value={translation.audioFormat} />
  );

  /**
   *
   * @param {*} data
   */
  const rowExpansionTemplate = (data) => {
    if (data) {
      const title = `Synthèses vocales du bulletin daté du ${new UtcDate(new Date(data.date)).format(process.env.REACT_APP_DATE_FORMAT)}`;
      return (
        <div className="orders-subtable">
          <h5>
            {title}
          </h5>
          <DataTable value={data.translations}>
            <Column style={{ textAlign: 'center' }} body={bodyPlayPause} header="Lecteur audio" />
            <Column style={{ textAlign: 'center' }} body={bodyLanguage} header="Langue" />
            <Column style={{ textAlign: 'center' }} field="voice" header="Voix" />
            <Column style={{ textAlign: 'center' }} field="frequency" header="Echantillonage" />
            <Column style={{ textAlign: 'center' }} body={bodyAudioFormat} header="Format audio" />
            <Column style={{ textAlign: 'center' }} body={bodyTranslationStatus} header="Statut" />
            <Column style={{ textAlign: 'center' }} body={bodyTranslationBrodcasting} header="Diffusion" />
            <Column style={{ textAlign: 'center' }} body={bodyOpenSettings} header="Texte synthétisé" />
          </DataTable>
        </div>
      );
    }
    return null;
  };

  /**
   * Template of settings.
   */
  const renderDecodedText = () => {
    if (decodedTextToDisplay) {
      const sentences = decodedTextToDisplay.split('.');
      return (
        <div className="settings-wrapper">
          <div className="container">
            {
              sentences.map((sentence) => (<p className="DecodedSentence">{String(sentence).concat('.')}</p>))
            }
          </div>
        </div>
      );
    }
    return null;
  };

  /**
   * Template of the header template.
   */
  // const tableHeader = () => (
  //   <div className="table-header">
  //     <h3>Bulletins météo reçus</h3>
  //   </div>
  // );

  /**
   * ction when the row is closed.
   * @param {*} bulletin Bulletin in the row.
   */
  const onRowCollapse = ({ data: bulletin }) => {
    if (bulletin) {
      const bulletinIndex = bulletins.findIndex((item) => item.name === bulletin.name);
      const newBulletinsList = [...bulletins];
      newBulletinsList[bulletinIndex].isRowOpen = false;
      setBulletins(newBulletinsList);
    }
    return null;
  };

  /**
   * Action when the row is opened.
   * @param {*} bulletin Bulletin in the row.
   */
  const onRowExpand = ({ data: bulletin }) => {
    if (bulletin) {
      const bulletinIndex = bulletins.findIndex((item) => item.name === bulletin.name);
      const newBulletinsList = [...bulletins];
      newBulletinsList[bulletinIndex].isRowOpen = true;
      setBulletins(newBulletinsList);
    }
    return null;
  };

  /**
   * Set the class name of the row
   * @param {*} rowData
   */
  const rowClassName = () => ({ rowOpen: true });

  // const header = tableHeader();
  return (
    <div className="datatable-rowexpansion-demo">
      <audio
        className="audio-element"
        src={audioUrl}
        type="audio/mpeg"
      />
      <Toast ref={toast} />
      <Dialog header="Texte synthétisé" visible={showModal} style={{ width: '50vw' }} dismissableMask onHide={() => setShowModal(false)}>
        {renderDecodedText()}
      </Dialog>
      <div className="card">
        <DataTable
          value={bulletins}
          expandedRows={expandedRows}
          onRowToggle={(e) => setExpandedRows(e.data)}
          onRowCollapse={(e) => onRowCollapse(e)}
          onRowExpand={(e) => onRowExpand(e)}
          rowExpansionTemplate={rowExpansionTemplate}
          dataKey="name"
          header={props.header()}
          globalFilter={props.globalFilter}
          rowClassName={rowClassName}
          paginator
          rows={10}
          rowsPerPageOptions={[10, 30, 60]}
          paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
          emptyMessage="Il n'y a pas de bulletin météo."
        >
          <Column headerStyle={{ background: '#0d47a1' }} expander style={{ width: '2em' }} />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyDate} header="Date" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyBulletinStatus} header="Statut" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyProtocol} header="Protocole" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyTranslationsLanguage} header="Messages audio" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyBulletinBrodcasting} header="Diffusion en cours" />
        </DataTable>
      </div>
    </div>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(BulletinTable);
