/* eslint-disable react/prop-types */
/* eslint-disable class-methods-use-this */
/* eslint-disable max-len */
import React, {
  useState, useEffect, useCallback, useRef,
} from 'react';
import { connect } from 'react-redux';
import { Toast } from 'primereact/toast';
import { Dropdown } from 'primereact/dropdown';
import { RadioButton } from 'primereact/radiobutton';
import { Calendar } from 'primereact/calendar';
import { Button } from 'primereact/button';
import { Sidebar } from 'primereact/sidebar';
import { MultiSelect } from 'primereact/multiselect';
import { setError } from '../../../store/error/errorAction';
import EventLogService from '../../../services/eventLogService';
import EventLogBackupService from '../../../services/eventLogBackupService';
import EventLogTable from '../../components/EventLogTable';
// import WebsocketService from '../../../services/websocketService';
// import WebSocketClientEventListener from '../../../tools/websocket/websocket-eventlistener';
// import EventLog from '../../../entities/eventLog';
import './styles.scss';

const EventLogPage = ({ setErrorInState }) => {
  const [eventLogs, setEventLogs] = useState([]);
  const [eventLogsBackups, setEventLogsBackups] = useState([]);
  const [eventLogMonth, setEventLogMonth] = useState('');
  const [eventLogFile, setEventLogFile] = useState('TEST');
  const [filterByDate, setFilterByDate] = useState({
    startDate: null,
    endDate: null,
  });
  const [doRequest, setDoRequest] = useState(false);
  const [eventsLogsToShow, setEventLogsToShow] = useState('now');
  const toast = useRef(null);
  const [showFilterPanel, setShowFilterPanel] = useState(false);
  const [logLevel] = useState(['TRACE', 'DEBUG', 'INFO', 'WARNING', 'ERROR', 'FATAL', 'CRITICAL', 'EXCEPTION']);
  const [selectedLogLevel, setSelectedLogLevel] = useState(logLevel);

  // eslint-disable-next-line consistent-return
  const getEventLogs = useCallback(async () => {
    try {
      const SERVICE = new EventLogService();
      const LOGS = await SERVICE.get({});
      setEventLogs(LOGS);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  }, [setErrorInState]);

  const getEventLogsBackup = useCallback(async () => {
    try {
      const SERVICE = new EventLogBackupService();
      const BACKUPS = await SERVICE.get({});
      setEventLogsBackups(BACKUPS);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  }, [setErrorInState]);

  const getEventLogsAfterSocketsUpdate = useCallback(() => {
    if (doRequest) {
      getEventLogs();
      setDoRequest(false);
    }
  }, [doRequest, getEventLogs]);

  useEffect(() => {
    getEventLogsAfterSocketsUpdate();
  }, [getEventLogsAfterSocketsUpdate]);

  useEffect(() => {
    getEventLogsBackup();
    getEventLogs();
  }, [getEventLogs, getEventLogsBackup]);

  const EventLogTableHeader = () => (
    <div className="table-header">
      <h3>Liste des évènements</h3>
    </div>
  );

  const download = () => {
    const SERVICE = new EventLogService();
    if (eventsLogsToShow === 'old') {
      if ((eventLogMonth.length >= 2) && (eventLogFile.length >= 2)) {
        const FILENAME = eventLogFile;
        SERVICE.download(eventLogMonth, FILENAME);
      }
    } else if (eventsLogsToShow === 'now') {
      SERVICE.download();
    }
  };

  const showEventLog = async () => {
    const SERVICE = new EventLogService();
    if (eventsLogsToShow === 'old') {
      SERVICE.getOldEventLog(eventLogMonth, eventLogFile);
      try {
        const LOGS = await SERVICE.getOldEventLog(eventLogMonth, eventLogFile);
        setEventLogs(LOGS);
      } catch (e) {
        setErrorInState(e.response ? e.response.status : e.status || 500);
      }
    } else if (eventsLogsToShow === 'now') {
      try {
        if (filterByDate.startDate && filterByDate.endDate) {
          if (filterByDate.startDate.timestamp > filterByDate.endDate.timestamp) {
            toast.current.show({ severity: 'warn', summary: "Message d'avertissement", detail: 'La date de début ne peux pas être supèrieure à la date de fin' });
          } else {
            const LOGS = await SERVICE.get({
              from: filterByDate.startDate.timestamp,
              to: filterByDate.endDate.timestamp,
            });
            console.log(LOGS);
            setEventLogs(LOGS);
          }
        } else {
          const LOGS = await SERVICE.get({ });
          setEventLogs(LOGS);
        }
      } catch (e) {
        setErrorInState(e.response ? e.response.status : e.status || 500);
      }
    }
  };

  const updateLogFile = () => eventLogsBackups.filter((backup) => backup.month === eventLogMonth).map((backup) => backup.names)[0];

  const updateDatesForFilter = (e) => {
    const { name, value } = e.target;

    setFilterByDate({
      ...filterByDate,
      [name]: {
        date: value,
        timestamp: Date.parse(value),
      },
    });
  };

  console.log(selectedLogLevel);

  return (
    <main className="page page-EventLogs page-container">
      <Toast ref={toast} baseZIndex={10000} />
      <section>
        <Sidebar className="event-side-bar" visible={showFilterPanel} onHide={() => setShowFilterPanel(false)} style={{ left: '16.5rem', paddingTop: '75px' }}>
          <h3>Filtre d'évènements</h3>
          <h5>Sélection du type</h5>
          <div className="event-type-selection mb">
            <RadioButton value="old" inputId="event-log-datetime-old-value" name="event-log-datetime" onChange={(e) => setEventLogsToShow(e.value)} checked={eventsLogsToShow === 'old'} />
            <label htmlFor="event-log-datetime-old-value" id="event-log-datetime-old">Evenements antérieurs</label>
          </div>
          <div className="event-type-selection">
            <RadioButton value="now" inputId="event-log-datetime-now-value" name="event-log-datetime" onChange={(e) => setEventLogsToShow(e.value)} checked={eventsLogsToShow === 'now'} />
            <label htmlFor="event-log-datetime-now-value" id="event-log-datetime-now">Evenements actuels</label>
          </div>
          {
            eventsLogsToShow === 'old' ? (
              <div className="select-file-container">
                <hr />
                <h5>Sélection des fichiers</h5>
                <Dropdown className="mounth-dropdown" value={eventLogMonth} options={eventLogsBackups.map((backup) => backup.month)} onChange={(event) => setEventLogMonth(event.value)} required placeholder="Sélectionnez un mois" />
                <Dropdown className="file-dropdown" value={eventLogFile} options={updateLogFile()} onChange={(event) => setEventLogFile(event.value)} required placeholder="Sélectionnez un fichier" />
                <Button label="Télécharger" icon="pi pi-download" onClick={() => download()} />
              </div>
            ) : (
              null
            )
          }
          <hr />
          <div className="select-time-container">
            <h5>Sélection d'une plage horaire</h5>
            <div className="select-start-date">
              <p>A partir de :</p>
              <Calendar
                name="startDate"
                showTime
                readOnlyInput
                value={filterByDate.startDate && filterByDate.startDate.date}
                dateFormat="dd/mm/yy"
                timeOnly
                placeholder="Horaire de début"
                onChange={(e) => updateDatesForFilter(e)}
              />
            </div>
            <div className="select-end-date">
              <p>Jusqu'à :</p>
              <Calendar
                name="endDate"
                showTime
                readOnlyInput
                value={filterByDate.endDate && filterByDate.endDate.date}
                dateFormat="dd/mm/yy"
                timeOnly
                placeholder="Horaire de fin"
                onChange={(e) => updateDatesForFilter(e)}
              />
            </div>
          </div>
          <hr />
          <div className="criticity-container">
            <h5>Criticité</h5>
            <MultiSelect value={selectedLogLevel} options={logLevel} onChange={(e) => setSelectedLogLevel(e.value)} />
          </div>
          {
            eventsLogsToShow !== 'old' ? (
              <>
                <hr />
                <Button label="Télécharger" icon="pi pi-download" onClick={() => download()} />
              </>

            )
              : ''
          }
          <hr />
          <Button label="Rechercher" icon="pi pi-search" onClick={() => showEventLog()} />
        </Sidebar>
        <Button icon="pi pi-filter" onClick={() => setShowFilterPanel(true)} style={{ marginBottom: '2rem' }} label="Filtrer" />
        <EventLogTable
          header={EventLogTableHeader}
          values={eventLogs}
          setDoRequest={setDoRequest}
          refreshData
        />
      </section>
    </main>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(EventLogPage);
