/* eslint-disable max-len */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-lonely-if */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable react/prop-types */
/* eslint-disable-next-line react/jsx-one-expression-per-line */
import React, {
  useState, useRef, useEffect,
} from 'react';
import { Column } from 'primereact/column';
import { Badge } from 'primereact/badge';
import { DataTable } from 'primereact/datatable';
import { Dialog } from 'primereact/dialog';
import { Toast } from 'primereact/toast';
import { Button } from 'primereact/button';
import { connect } from 'react-redux';
import { Tag } from 'primereact/tag';
import { Tooltip } from 'primereact/tooltip';
import { v4 as uuidv4 } from 'uuid';
import Flag from 'react-world-flags';
import { setError } from '../../../store/error/errorAction';
import './styles.scss';
import parseCountry from '../../../tools/country-code-parser';
import UtcDate from '../../../entities/date';

const AdvancedBulletinTable = (props) => {
  const [bulletins, setBulletins] = useState([]);
  const toast = useRef(null);
  const [expandedBulletinsRows, setExpandedBulletinsRows] = useState(null);
  const [expandedTranslationsRows, setExpandedTranslationsRows] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [weatherTelemetriesToDisplay, setWeatherTelemetriesToDisplay] = useState([]);

  useEffect(() => {
    if (props.values) {
      setBulletins(props.values);
    }
  }, [props]);

  const bodyLanguage = (bulletin) => {
    if (bulletin && bulletin.language) {
      return (
        <Flag
          code={parseCountry(bulletin.language)}
          height={20}
        />
      );
    }
    return <span>Langue en cours de récupération</span>;
  };

  const bodyDate = (data) => {
    if (data) {
      return (
        <>
          {
            new UtcDate(new Date(data.date)).format(process.env.REACT_APP_DATE_FORMAT)
          }
        </>
      );
    }
    return null;
  };

  const bodyBulletinStatus = (bulletin) => {
    if (bulletin) {
      if (bulletin.status === 'ERROR' || bulletin.error) {
        const ID = 'badge-'.concat(uuidv4().toString().replace('-', ''));
        return (
          <>
            <Tooltip target={'.'.concat(ID)} content={bulletin.error} position="bottom" />
            <Badge id={ID} value={bulletin.status} severity="danger" className={`customer-badge status-${bulletin.status} ${ID}`} />
          </>
        );
      }
      return (<Badge value={bulletin.status} />);
    }
    return null;
  };

  const bodyTranslationStatus = (translation) => {
    const ID = 'badge-'.concat(uuidv4().toString().replace('-', ''));
    if (translation) {
      if (translation.status === 'ERROR' || translation.error) {
        return (
          <>
            <Tooltip target={'.'.concat(ID)} content={translation.error} position="bottom" />
            <Badge id={ID} value={translation.status} severity="danger" />
          </>
        );
      }
      if (translation.status === 'PROCESSING') {
        return (
          <Badge value={translation.status} severity="warning" />
        );
      }

      if (translation.isAudioFileExists) {
        return (
          <Badge value="Disponible" severity="success" />
        );
      }
      return (
        <>
          <Tooltip target={'.'.concat(ID)} content="Audio file not exists." position="bottom" />
          <Badge id={ID} value="ERROR" severity="danger" className={`status-${translation.status} ${ID}`} />
        </>
      );
    }
    return null;
  };

  const bodyProtocol = (bulletin) => {
    if (bulletin) {
      return (<Tag style={{ fontSize: '1rem', background: '#3f51b5' }} value={bulletin.report_type} />);
    }
    return null;
  };

  /**
   *
   * @param {*} data
   */
  const rowExpansionBroadcastTemplate = (data) => {
    if (data) {
      const title = 'Liste des diffusions';
      return (
        <div className="orders-subtable">
          <h5>
            {title}
          </h5>
          <DataTable
            value={data.broadcasts}
            dataKey="identifier"
            globalFilter={props.globalFilter}
            rowClassName={rowClassName}
            emptyMessage="Il n'y a pas de diffusion."
          >
            <Column style={{ textAlign: 'center' }} field="identifier" header="ID" />
            <Column style={{ textAlign: 'center' }} body={bodyDate} header="Date" />
            <Column style={{ textAlign: 'center' }} field="broadcastType" header="Type" />
          </DataTable>
        </div>
      );
    }
    return null;
  };

  /**
   *
   * @param {*} data
   */
  const rowExpansionTranslationTemplate = (data) => {
    if (data) {
      const title = `Synthèses vocales du bulletin daté du ${new UtcDate(new Date(data.date)).format(process.env.REACT_APP_DATE_FORMAT)}`;
      return (
        <div className="orders-subtable">
          <h5>
            {title}
          </h5>
          <DataTable
            value={data.translations}
            dataKey="identifier"
            expandedRows={expandedTranslationsRows}
            onRowToggle={(e) => setExpandedTranslationsRows(e.data)}
            onRowCollapse={(e) => onTranslationRowCollapse(e)}
            onRowExpand={(e) => onRowTranslationsExpand(e)}
            rowExpansionTemplate={rowExpansionBroadcastTemplate}
            rowClassName={rowClassName}
            emptyMessage="Il n'y a pas de traducton."
          >
            <Column headerStyle={{ background: '#3A72A3' }} expander style={{ width: '2em' }} />
            <Column headerStyle={{ background: '#3A72A3' }} style={{ textAlign: 'center' }} body={bodyLanguage} header="Langue" />
            <Column headerStyle={{ background: '#3A72A3' }} style={{ textAlign: 'center' }} field="voice" header="Voix" />
            <Column headerStyle={{ background: '#3A72A3' }} style={{ textAlign: 'center' }} body={bodyTranslationStatus} header="Statut" />
          </DataTable>
        </div>
      );
    }
    return null;
  };

  /**
   * Template of the header template.
   */
  // const tableHeader = () => (
  //   <div className="table-header">
  //     <h3>Bulletins météo reçus</h3>
  //   </div>
  // );

  /**
   * ction when the row is closed.
   * @param {*} bulletin Bulletin in the row.
   */
  const onBulletinRowCollapse = ({ data: bulletin }) => {
    if (bulletin) {
      const bulletinIndex = bulletins.findIndex((item) => item.name === bulletin.name);
      const newBulletinsList = [...bulletins];
      newBulletinsList[bulletinIndex].isRowOpen = false;
      setBulletins(newBulletinsList);
    }
    return null;
  };

  /**
   * ction when the row is closed.
   * @param {*} bulletin Bulletin in the row.
   */
  const onTranslationRowCollapse = ({ data: translation }) => {
    if (translation) {
      let bulletinIndex = -1;
      let translationIndex = -1;
      for (let indexB = 0; indexB < bulletins.length; indexB += 1) {
        translationIndex = bulletins[indexB].translations.findIndex((translate) => translate.identifier === translation.identifier);
        if (translationIndex > -1) {
          bulletinIndex = bulletins[indexB].identifier;
          return;
        }
      }
      const newList = [...bulletins];
      newList[bulletinIndex].translations[translationIndex].isRowOpen = false;
      setBulletins(newList);
    }
  };

  /**
   * Action when the row is opened.
   * @param {*} bulletin Bulletin in the row.
   */
  const onRowBulletinExpand = ({ data: bulletin }) => {
    if (bulletin) {
      const bulletinIndex = bulletins.findIndex((item) => item.name === bulletin.name);
      const newBulletinsList = [...bulletins];
      newBulletinsList[bulletinIndex].isRowOpen = true;
      setBulletins(newBulletinsList);
    }
  };

  /**
   * Action when the row is opened.
   * @param {*} bulletin Bulletin in the row.
   */
  const onRowTranslationsExpand = ({ data: transaltion }) => {
    if (transaltion) {
      let bulletinIndex = -1;
      let translationIndex = -1;
      for (let indexB = 0; indexB < bulletins.length; indexB += 1) {
        translationIndex = bulletins[indexB].translations.findIndex((translate) => translate.identifier === transaltion.identifier);
        if (translationIndex > -1) {
          bulletinIndex = bulletins[indexB].identifier;
          return;
        }
      }
      const newList = [...bulletins];
      newList[bulletinIndex].translations[translationIndex].isRowOpen = true;
      setBulletins(newList);
    }
  };

  const showWeatherTelemetriesModal = (e, weatherTelemetries) => {
    setShowModal(true);
    setWeatherTelemetriesToDisplay(weatherTelemetries);
  };

  const bodyOpenTelemetries = (bulletin) => (
    <Button label={bulletin.weatherTelemetries.length} className="p-button-rounded" onClick={(e) => showWeatherTelemetriesModal(e, bulletin.weatherTelemetries)} />
  );

  const renderWeatherTelemetries = () => {
    if (weatherTelemetriesToDisplay) {
      return (
        <div className="settings-wrapper">
          <div className="container">
            <DataTable
              value={weatherTelemetriesToDisplay}
              dataKey="identifier"
              paginator
              rows={5}
              rowsPerPageOptions={[5, 10, 20]}
              paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
              emptyMessage="Il n'y a pas de télémesures."
            >
              <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} field="name" header="Télémesure" />
              <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} field="value" header="Valeur" />
            </DataTable>
          </div>
        </div>
      );
    }
    return null;
  };

  /**
   * Set the class name of the row
   * @param {*} rowData
   */
  const rowClassName = () => ({ rowOpen: true });

  // const header = tableHeader();

  return (
    <div className="datatable-rowexpansion-demo">
      <Toast ref={toast} />
      <Dialog header="Télémesures du bulletin" visible={showModal} style={{ width: '50vw' }} dismissableMask onHide={() => setShowModal(false)}>
        {renderWeatherTelemetries()}
      </Dialog>
      <div className="card">
        <DataTable
          value={bulletins}
          expandedRows={expandedBulletinsRows}
          onRowToggle={(e) => setExpandedBulletinsRows(e.data)}
          onRowCollapse={(e) => onBulletinRowCollapse(e)}
          onRowExpand={(e) => onRowBulletinExpand(e)}
          rowExpansionTemplate={rowExpansionTranslationTemplate}
          dataKey="name"
          header={props.header()}
          rowClassName={rowClassName}
          paginator
          rows={10}
          rowsPerPageOptions={[10, 30, 60]}
          paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
          emptyMessage="Il n'y a pas de bulletin météo."
        >
          <Column headerStyle={{ background: '#0d47a1' }} expander style={{ width: '2em' }} />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyDate} header="Date" />
          {/* <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} field="airport.name" header="Aéroport" /> */}
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyBulletinStatus} header="Statut" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyProtocol} header="Protocole" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'center' }} body={bodyOpenTelemetries} header="Télémesures" />
        </DataTable>
      </div>
    </div>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(AdvancedBulletinTable);
