import WebSocketClient from '../tools/websocket/websocket-client';

/**
 * Class that represents a WebSocket Client Factory.
 * @description Used to create websocket client.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
export default class WebsocketClientFactory {
  /**
   * Websocket Client Factory default constructor.
   */
  constructor() {
    /**
     * Websocket URL.
     */
    this.url = null;

    /**
     * Websocket event listener.
     */
    this.eventListener = null;
  }

  /**
   * Set the websocket URL connection.
   * @param {String} url The websocket URL connection.
   */
  withUrl(url) {
    this.url = url;
    return this;
  }

  /**
   * Set the  websocket event listener.
   * @param {*} eventListener The websocket event listener.
   */
  withEventListener(eventListener) {
    this.eventListener = eventListener;
    return this;
  }

  /**
   * Build a websocket instance.
   * @return The websocket instance created.
   */
  build() {
    if (this.url !== null && this.url !== undefined) {
      const websocket = new WebSocketClient(this.url);
      if (this.eventListener != null) {
        websocket.setEventListener(this.eventListener);
      }
      return websocket;
    }
    throw new Error('Websocket URL is not defined.');
  }
}
