/* eslint-disable react/prop-types */
import React from 'react';
import { Tag } from 'primereact/tag';
import { Card } from 'primereact/card';
import { ProgressSpinner } from 'primereact/progressspinner';
import './styles.scss';

const RtcGatewayCard = ({ rtcGateways }) => {
  const renderRtcLine = (rtcLine) => {
    if (rtcLine && rtcLine.portType === 'FXO') {
      return (
        <div key={rtcLine.identifier} className="rtc-line">
          <h5>{rtcLine.phoneNumber ? rtcLine.phoneNumber : 'Non attribué'}</h5>
          <div className="rtc-line-port">
            {
              (!rtcLine.isCidRestricted) ? <Tag className="p-mr-2" severity="success" value="En ligne" /> : <Tag className="p-mr-2" severity="warning" value="Déconnecté" />
            }
            <div className={String('port-status-').concat(!rtcLine.isCidRestricted)} />
            <div className="activity">
              {
                (rtcLine.isBroadcasting) ? <Tag className="p-mr-2" severity="success" value="Diffusion" /> : <Tag className="p-mr-2" severity="warning" value="En attente" />
              }
            </div>
          </div>
        </div>
      );
    }
    return null;
  };

  const renderRtcGateway = (rtcGateway) => (
    <div className="rtc-gateway-container">
      <div className="rtc-lines-container">
        {
          rtcGateway.rtcLines.map((rtcLine) => renderRtcLine(rtcLine))
        }
      </div>
    </div>
  );

  const renderRtcGateways = () => {
    if (!rtcGateways) {
      return <ProgressSpinner />;
    }
    if (rtcGateways[0].rtcLines.length === 0) {
      return <p className="no-data">Aucun service trouvé</p>;
    }

    if (rtcGateways.length > 0) {
      if (rtcGateways[0].rtcLines.length > 0) {
        return (
          <div className="template-rtc-gateway-container">
            <div className="name-container">
              {
              rtcGateways.map((rtcGateway) => renderRtcGateway(rtcGateway))
            }
            </div>
          </div>
        );
      }
    }

    return null;
  };

  return (
    <Card
      title="Passerelles Téléphoniques"
      // style={{ width: '30rem' }}
    >
      { renderRtcGateways() }
    </Card>
  );
};

export default RtcGatewayCard;
