/* eslint-disable react/prop-types */
import React from 'react';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { Badge } from 'primereact/badge';
import { resetUser } from '../../../../store/user/userAction';
import './styles.css';
import Tooltip from '../../Tooltip';

// badge={String(alertsFromState.length)}
//           badgeClassName={(alertsFromState.length === 0) ? 'p-badge-success' : 'p-badge-danger'}

const SideBarItem = ({
  text, urlSrc, path, logout, badge,
}) => {
  const renderItems = () => {
    if (text === 'Déconnexion') {
      return (
        <li className="SideBarItem">
          <NavLink activeClassName="item-selected" onClick={() => logout()} to={path}>
            <div className="sidebar-item">
              <img className="SideBarItemIcon" alt="icon" src={urlSrc} />
              { text }
              { badge ? <Badge severity="danger" value={badge} /> : null }
            </div>
          </NavLink>
        </li>
      );
    }
    return (
      <li className="SideBarItem">
        <Tooltip content={text} position="top" />
        <NavLink activeClassName="item-selected" to={path}>
          <div className="sidebar-item">
            <img className="SideBarItemIcon" alt="icon" src={urlSrc} />
            { text }
            { badge ? <Badge severity="danger" value={badge} /> : null }
          </div>
        </NavLink>
      </li>
    );
  };

  return (
    <>
      {renderItems()}
    </>
  );
};

const mapDispatchToProps = (dispatch) => ({
  // eslint-disable-next-line no-undef
  logout: () => dispatch(resetUser()),
});

export default connect(null, mapDispatchToProps)(SideBarItem);
