/* eslint-disable max-len */
export default class Route {
  constructor(title, path, component, exact = true, isVisible = true, icon = null, allowedGroups = null, badge = null) {
    this.title = title;
    this.path = path;
    this.component = component;
    this.exact = exact;
    this.allowedGroups = allowedGroups;
    this.isVisible = isVisible;
    this.icon = icon;
    this.badge = badge;
  }
}
