import WebSocketClientEventListener from './websocket-eventlistener';

/**
 * Class that represents a websocket client.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
export default class WebSocketClient {
  /**
   * Web socket client.
   * @param {String} url The web socket URL (ex: ws://localhost:9600).
   */
  constructor(url) {
    this.url = url;
    this.connection = null;
    this.eventListener = null;
  }

  /**
   * Private function that bind all events.
   */
  bindEvents() {
    if ((this.connection !== null) && (this.eventListener !== null)) {
      this.connection.onopen = (event) => {
        this.eventListener.onOpen(event);
      };
      this.connection.onclose = (event) => {
        this.eventListener.onClose(event);
      };
      this.connection.onerror = (event) => {
        this.eventListener.onError(event);
      };
      this.connection.onmessage = (event) => {
        this.eventListener.onMessage(event);
      };
    } else {
      throw Error('"connection" or "eventListerner" properties are not set.');
    }
  }

  /**
   * Connect the websocket client to the websocket server.
   */
  connect() {
    this.connection = new WebSocket(this.url);
    try {
      this.bindEvents();
    } catch (error) {
      // console.error(error);
    }
  }

  /**
   * Disconnect the websocket client to the websocket server.
   */
  disconnect() {
    this.connection.close();
  }

  /**
   * Set an event listener.
   * @param {WebSocketClientEventListener} eventListener The event listener.
   */
  setEventListener(eventListener) {
    if (eventListener instanceof WebSocketClientEventListener) {
      this.eventListener = eventListener;
      if (this.connection !== null && this.connection !== undefined) {
        this.bindEvents();
      }
    } else {
      throw Error('Unexpected type for event listener.');
    }
  }
}
