/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, { useMemo, useRef, useState } from 'react';
import { Button } from 'primereact/button';
import { Toast } from 'primereact/toast';
import { v4 as uuidv4 } from 'uuid';
import { useCookies } from 'react-cookie';
import { connect } from 'react-redux';
import { setError } from '../../../store/error/errorAction';
import AdditionalMessageService from '../../../services/additionalMessageService';

const Player = ({
  audioElement, setAudioURL, blob, setBlob, setErrorInState, setAdditionalsMessages,
}) => {
  const [cookies] = useCookies();
  const toast = useRef(null);
  const ADDITIONAL_MESSAGE_SERVICE = useMemo(() => new AdditionalMessageService(), []);
  const [messageHasBeenListened, setMessageHasBeenListened] = useState(false);

  const playerManager = () => {
    if (audioElement) {
      setMessageHasBeenListened(true);
      audioElement.play();
    }
  };

  const cancelRecording = () => {
    setBlob(null);
    setAudioURL(null);
    toast.current.show({ severity: 'info', summary: "Message d'information", detail: 'Processus annulé' });
  };

  const submitRecord = () => {
    const formData = new FormData();

    const filename = `${uuidv4()}.wav`;
    formData.append('file', blob, filename);

    ADDITIONAL_MESSAGE_SERVICE.create(formData, cookies.jwt).then((response) => {
      const additionalMessage = response;
      additionalMessage.isRecordedVoice = true;

      // eslint-disable-next-line max-len
      ADDITIONAL_MESSAGE_SERVICE.update(additionalMessage).then((_response) => {
        console.log(_response);
        setBlob(null);
        setAudioURL(null);
        setAdditionalsMessages((messages) => [...messages, additionalMessage]);
        toast.current.show({ severity: 'success', summary: 'Message de succès', detail: 'Message enregistré' });
      }).catch((e) => {
        setErrorInState(e.response ? e.response.status : e.status || 500);
      });
    }).catch((e) => {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    });
  };

  return (
    <div className="player-container">
      <Toast ref={toast} baseZIndex={10000} />
      <Button
        className="p-button-secondary"
        label="Ecouter"
        icon="pi pi-play"
        onClick={() => playerManager()}
      />
      <Button
        className="p-button-success"
        label="Valider"
        icon="pi pi-check"
        disabled={!messageHasBeenListened}
        onClick={() => submitRecord()}
      />
      <Button
        className="p-button-danger"
        label="Annuler"
        icon="pi pi-times"
        onClick={() => cancelRecording()}
      />
    </div>
  );
};

const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(Player);
