// import axios from 'axios';
import AbstractService from './abstractService';
import ApplicationManifest from '../entities/ApplicationManifest';

export default class ApplicationManifestService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/version'));
  }

  get() {
    return new Promise((resolve, reject) => {
      super.get()
        .then((result) => {
          resolve(ApplicationManifest.parse(result));
        }).catch((e) => {
          reject(e);
        });
    });
  }
}
