import AbstractService from './abstractService';

export default class DateTimeMethodsService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/datetime'));
  }

  get(filters) {
    return new Promise((resolve, reject) => {
      super.get(filters)
        .then((result) => {
          resolve(result.utc.epochSecond * 1000);
        })
        .catch((err) => {
          reject(err);
        });
    });
  }
}
