/* eslint-disable object-curly-newline */
/* eslint-disable max-len */
import React, { useState, useEffect, useCallback } from 'react';
import { Menubar } from 'primereact/menubar';
// import { Badge } from 'primereact/badge';
import { Tag } from 'primereact/tag';
import { Skeleton } from 'primereact/skeleton';
import { useSelector, connect } from 'react-redux';
import { setError } from '../../../store/error/errorAction';
import './styles.scss';
import DateTimeMethodsProvider from '../../../services/dateTimeService';
import UtcDate from '../../../entities/date';

const Header = ({ setErrorInState }) => {
  const usernameFromState = useSelector((state) => state.user.username);
  const userRolesFromState = useSelector((state) => state.user.roles);
  const [delayForUpdateTime] = useState(60000 * 10);
  const [timeToUpdate, setTimeToUpdate] = useState(null);
  const [time, setTime] = useState(null);
  const [mode] = useState('MODE AUTOMATIQUE');
  const [isActiveComplementaryMessage] = useState(false);

  const tick = useCallback(async () => {
    setTime(time + 1000);
  }, [time]);

  const getTimeFromServer = useCallback(async () => {
    try {
      const DateTimeService = new DateTimeMethodsProvider();
      const timestamp = await DateTimeService.get();
      setTime(timestamp);
      setTimeToUpdate(timestamp + delayForUpdateTime);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  }, [setErrorInState, delayForUpdateTime]);

  useEffect(() => {
    if (time === timeToUpdate) {
      getTimeFromServer();
    }
  }, [time, timeToUpdate, getTimeFromServer]);

  useEffect(() => {
    getTimeFromServer();
  }, [getTimeFromServer]);

  useEffect(() => {
    const timer = setInterval(tick, 1000);
    return () => clearInterval(timer);
  }, [tick]);

  const renderEngieIcon = () => (
    <div>
      <a href="./">
        <img
          src="/assets/images/logo-engie.svg"
          className="engie-logo"
          alt="React Bootstrap logo"
        />
      </a>
      {
        (usernameFromState && userRolesFromState) ? (
          <span className="mode">
            <Tag style={{ fontSize: '1rem' }} value={mode} severity="info" />
          </span>
        ) : null
      }
    </div>
  );

  const renderWelcomeMessage = () => {
    if (usernameFromState && userRolesFromState) {
      let additionalMessageText = 'Message Complémentaire ';
      if (isActiveComplementaryMessage) {
        additionalMessageText = additionalMessageText.concat('Activé');
      } else {
        additionalMessageText = additionalMessageText.concat('Désactivé');
      }
      return (
        <>
          <span className="mode">
            {
              isActiveComplementaryMessage ? (<Tag style={{ fontSize: '1rem' }} value={additionalMessageText} severity="info" />) : (<Tag style={{ fontSize: '1rem', color: '#9e9e9e', background: '#eeeeee55' }} value={additionalMessageText} severity="info" />)
            }
          </span>
          <span className="date">
            {
              time ? new UtcDate(new Date(time)).format(process.env.REACT_APP_DATE_FORMAT) : <Skeleton />
            }
          </span>
        </>
      );
    }
    return null;
  };

  return (
    <Menubar
      style={{ background: '#212121', color: '#fafafa' }}
      className="header-container"
      start={renderEngieIcon}
      end={renderWelcomeMessage}
    />
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  // resetAllAlertsInStore: () => dispatch(resetAlerts()),
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(Header);
