/**
 * Class that represents an airport.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
export default class Memory {
  constructor() {
    this.identifier = null;
    this.name = 'Mémoire';
    this.className = 'memory-container';
    this.used = null;
    this.free = null;
    this.total = null;
    this.measure = null;
    this.usagePercent = null;
  }

  /**
   * Function to convert a JSON to Airport object.
   * @param {JSON} json Airport as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.used !== undefined) {
          if (json.free !== undefined) {
            if (json.total !== undefined) {
              if (json.measure !== undefined) {
                const memory = new Memory();
                memory.identifier = json.identifier;
                memory.used = json.used;
                memory.free = json.free;
                memory.total = json.total;
                memory.measure = json.measure;
                memory.usagePercent = json.usagePercent;
                return memory;
              }
            }
          }
        }
      }
    }
    return null;
  }
}
