import CPU from './CPU';
import Memory from './Memory';
import StorageDisk from './StorageDisk';

export default class StapServer {
  constructor() {
    this.identifier = null;
    this.cpu = null;
    this.memory = null;
    this.storageDisks = [];
  }

  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        console.log(json);
        if (json.cpu !== undefined) {
          if (json.memory !== undefined) {
            if (json.storageDisks !== undefined) {
              const stapServer = new StapServer();
              stapServer.identifier = json.identifier;
              stapServer.cpu = CPU.parse(json.cpu);
              stapServer.memory = Memory.parse(json.memory);
              if (json.storageDisks != null && Array.isArray(json.storageDisks)) {
                for (let index = 0; index < json.storageDisks.length; index += 1) {
                  stapServer.storageDisks.push(StorageDisk.parse(json.storageDisks[index]));
                }
              }
              return stapServer;
            }
          }
        }
      }
    }
    return null;
  }
}
