/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, { useState } from 'react';
import { Button } from 'primereact/button';
import { ReactMic } from 'react-mic';
import './styles.scss';

const MessageRecording = ({ setAudioURL, setBlob }) => {
  const [isRecording, setIsRecording] = useState(false);

  const onStopRecording = (audio) => {
    setBlob(audio.blob);
    setAudioURL(audio.blobURL);
  };

  const renderStartStopRecordingButton = () => {
    if (isRecording) {
      return (
        <Button
          icon="pi pi-pause"
          onClick={() => setIsRecording(false)}
          label="Stoper l'enregistrement"
        />
      );
    }
    return (
      <Button
        icon="pi pi-circle-off"
        onClick={() => setIsRecording(true)}
        label="Lancer l'enregistrement"
      />
    );
  };

  return (
    <>
      <div className="record-message-container">
        <ReactMic
          record={isRecording}
          className="sound-wave"
          onStop={onStopRecording}
          // visualSetting="frequencyBars"
          // strokeColor="#000000"
          // backgroundColor="#FF4081"
        />
        <div className="recording-buttons-container">
          {renderStartStopRecordingButton()}
        </div>
      </div>
    </>
  );
};

export default MessageRecording;
