/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, { useState } from 'react';
import { TabView, TabPanel } from 'primereact/tabview';
import SynthesisMessageCreation from '../SynthesisMessageCreation';
import MessageRecording from '../MessageRecording';
import Player from '../Player';
import './styles.scss';

const MessageCreation = ({ setAdditionalsMessages }) => {
  const [audioURL, setAudioURL] = useState(null);
  const [blobURL, setBlobURL] = useState(null);
  const [blob, setBlob] = useState(null);
  const audioElement = document.getElementsByClassName('audio-element')[0];

  const renderMessageRecordingOrPlayer = () => {
    if (audioURL) {
      return (
        <Player
          setBlob={setBlob}
          blob={blob}
          audioElement={audioElement}
          setAudioURL={setAudioURL}
          setAdditionalsMessages={setAdditionalsMessages}
        />
      );
    }
    return (
      <MessageRecording setBlob={setBlob} setAudioURL={setAudioURL} />
    );
  };

  return (
    <div className="creation-message-container">
      <audio
        className="audio-element"
        src={audioURL && audioURL}
        type="audio/webm;codecs=opus"
      />
      <TabView className="tabview-custom">
        <TabPanel header="Texte à synthétisé">
          <SynthesisMessageCreation
            setBlobURL={setBlobURL}
            blobURL={blobURL}
            setAdditionalsMessages={setAdditionalsMessages}
          />
        </TabPanel>
        <TabPanel header="Enregistrement audio">
          {renderMessageRecordingOrPlayer()}
        </TabPanel>
      </TabView>
    </div>
  );
};

export default MessageCreation;
