/**
 * Class that represents an airport.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
export default class CPU {
  constructor() {
    // this.identifier = null;
    this.name = 'CPU';
    this.className = 'cpu-container';
    this.usagePercent = null;
  }

  /**
   * Function to convert a JSON to Airport object.
   * @param {JSON} json Airport as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.usagePercent !== undefined) {
          const cpu = new CPU();
          cpu.identifier = json.identifier;
          cpu.usagePercent = json.usagePercent;
          return cpu;
        }
      }
    }
    return null;
  }
}
