import WebsocketClientFactory from '../factories/websocketFactory';

/**
 * Class that represents a WebSocket service.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
export default class WebsocketService {
  static singleton = null;

  /**
   * If url parameter is set, then try to get the existing websocket client
   * else create them.
   * Else if url parameter is not set (url=null), the wbesocket instance is
   * given and it's can be null. If getInstance() == null, then use getInstance(url) instead.
   * @return {}
   */
  static getInstance() {
    if (WebsocketService.singleton === null) {
      const FACTORY = new WebsocketClientFactory();
      WebsocketService.singleton = FACTORY.withUrl(process.env.REACT_APP_WEBSOCKET_URL).build();
    }
    return WebsocketService.singleton;
  }
}
