import axios from 'axios';
import * as userAction from './userActionType';
import { setUser } from './userAction';

const initialState = {
  firstname: null,
  lastname: null,
  username: null,
  roles: null,
  jwt: null,
};

export default function userReducer(state = initialState, action) {
  switch (action.type) {
    case userAction.SET_USER:
      return {
        ...state,
        firstname: action.data.firstname,
        lastname: action.data.lastname,
        username: action.data.username,
        roles: action.data.roles,
        jwt: action.data.jwt,
      };
    case userAction.RESET_USER:
      return {
        firstname: initialState.firstname,
        lastname: initialState.lastname,
        username: initialState.username,
        roles: initialState.roles,
      };
    default:
      return state;
  }
}

// Thunk function ( API Request )
export function connectUser(username, password) {
  return async function connectUserThunk(dispatch) {
    try {
      const BASE_URL_AUTH_WS = process.env.REACT_APP_BASE_URL_WS;
      const response = await axios.post(`${BASE_URL_AUTH_WS}/login`, {
        username,
        password,
      });
      // On récupère les information dont on besoin afin de la dispatcher au reducer ci-dessus.
      const {
        uid, givenName, sn, groups,
      } = response.data.account;
      dispatch(setUser({
        username: uid, firstname: givenName, lastname: sn, role: groups, jwt: response.data.jws,
      }));
    } catch (e) {
      console.error(e);
    }
  };
}
