/* eslint-disable max-len */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable class-methods-use-this */
/* eslint-disable no-lonely-if */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable react/prop-types */
/* eslint-disable object-curly-newline */
/* eslint-disable-next-line react/jsx-one-expression-per-line */
import React, {
  useState, useRef, useEffect,
} from 'react';
import { Column } from 'primereact/column';
import { Badge } from 'primereact/badge';
import { DataTable } from 'primereact/datatable';
import { Toast } from 'primereact/toast';
import { connect } from 'react-redux';
import { setError } from '../../../store/error/errorAction';

import './styles.scss';
import UtcDate from '../../../entities/date';

const EventLogTable = (props) => {
  const [eventLogs, setEventLogs] = useState([]);
  const toast = useRef(null);

  useEffect(() => {
    if (props.values) {
      setEventLogs(props.values);
    }
  }, [props]);

  const bodyDate = (eventLog) => {
    if (eventLog) {
      return (
        <>
          {
            new UtcDate(new Date(eventLog.date)).format(process.env.REACT_APP_DATE_FORMAT)
          }
        </>
      );
    }
    return null;
  };

  const bodyLevel = (eventLog) => {
    switch (eventLog.level) {
      case 'TRACE':
        return <Badge value={eventLog.level} severity="danger" />;
      case 'DEBUG':
        return <Badge value={eventLog.level} severity="danger" />;
      case 'INFO':
        return <Badge value={eventLog.level} severity="primary" />;
      case 'WARNING':
        return <Badge value={eventLog.level} severity="warning" />;
      case 'ERROR':
        return <Badge value={eventLog.level} severity="danger" />;
      case 'CRITICAL':
        return <Badge value={eventLog.level} severity="danger" />;
      case 'FATAL':
        return <Badge value={eventLog.level} severity="danger" />;
      case 'EXCEPTION':
        return <Badge value={eventLog.level} severity="danger" />;
      case 'ALERT':
        return <Badge value={eventLog.level} severity="danger" />;
      default:
        break;
    }
    return eventLog.level;
  };

  /**
   * Set the class name of the row
   * @param {*} rowData
   */
  const rowClassName = () => ({ rowOpen: true });

  return (
    <div className="datatable-rowexpansion-demo">
      <Toast ref={toast} />
      <div className="card">
        <DataTable
          className="event-log-table"
          value={eventLogs}
          dataKey="date"
          header={props.header()}
          globalFilter={props.globalFilter}
          rowClassName={rowClassName}
          paginator
          rows={100}
          rowsPerPageOptions={[100, 200, 300, 400, 500]}
          paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
          emptyMessage="Il n'y a aucun évènement."
        >
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center', width: '15rem' }} style={{ textAlign: 'center', height: 'auto', padding: 0 }} body={bodyDate} header="Date" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center', width: '7rem' }} style={{ textAlign: 'center', height: 'auto', padding: 0 }} body={bodyLevel} header="Niveau" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center', width: '12rem' }} style={{ textAlign: 'center', height: 'auto', padding: 0 }} field="entity" header="Composant" />
          <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} style={{ textAlign: 'left', height: 'auto', padding: 0 }} field="message" header="Message" />
        </DataTable>
      </div>
    </div>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(EventLogTable);
