import AbstractService from './abstractService';

export default class WatchDogMethodsProvider extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/watch-dogs'));
  }

  getAllWatchDogs() {
    return new Promise((resolve, reject) => {
      super.get()
        .then((result) => {
          resolve(result);
        }).catch((e) => {
          reject(e);
        });
    });
  }
}
