import React from 'react';
import PropTypes from 'prop-types';
// import { useSelector } from 'react-redux';
import { useCookies } from 'react-cookie';
import { useSelector } from 'react-redux';
import { Route, Redirect } from 'react-router-dom';

const ProtectedRoute = ({
  path, component: Component, allowedGroups, exact,
}) => {
  // Force mandatory props.
  ProtectedRoute.propTypes = {
    exact: PropTypes.bool.isRequired,
    path: PropTypes.string.isRequired,
    allowedGroups: PropTypes.arrayOf(PropTypes.string).isRequired,
    component: PropTypes.func.isRequired,
  };

  const [cookies] = useCookies();
  const userRolesFromState = useSelector((state) => state.user.roles);

  const display = () => {
    if (cookies) {
      if (userRolesFromState) {
        for (let index = 0; index < userRolesFromState.length; index += 1) {
          if (allowedGroups.includes(userRolesFromState[index])) {
            return <Component />;
          }
        }
      }
    }
    return <Redirect to="/login" />;
  };

  return (
    <Route
      exact={exact}
      path={path}
      render={display}
    />
  );
};
export default ProtectedRoute;
