/* eslint-disable react/prop-types */
import React, { useState, useEffect } from 'react';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
// import { Button } from 'primereact/button';
import { Tag } from 'primereact/tag';
import './styles.scss';

const TableSettingsNetwork = ({ hards }) => {
  const [expandedRows, setExpandedRows] = useState(null);
  const [hardwares, setHardwares] = useState(hards);

  useEffect(() => {
    setHardwares(hards);
  }, [hards, setHardwares]);

  const ColumnStyle = {
    header: {
      width: '8em',
      textAlign: 'center',
    },
    body: {
      textAlign: 'center',
      overflow: 'visible',
    },
  };

  const rowExpansionTemplate = (data) => {
    const title = `Cartes réseau de ${data.description}`;
    return (
      <div className="orders-subtable">
        <h5>
          {title}
        </h5>
        <DataTable value={data.networkInterfaces}>
          <Column style={{ textAlign: 'center' }} field="macAddreess" header="Adresse MAC" />
          <Column style={{ textAlign: 'center' }} field="ipv4Address" header="Adresse IPv4" />
          <Column style={{ textAlign: 'center' }} field="netmask" header="Masque de sous-réseau" />
          <Column style={{ textAlign: 'center' }} field="gateway" header="Passerelle par défaut" />
          <Column style={{ textAlign: 'center' }} field="dns" header="Serveurs DNS" />
        </DataTable>
      </div>
    );
  };

  // const restartDevice = (rowData) => {
  //
  // };

  const renderStatusBody = (rowData) => {
    if (rowData.status !== 'UP') {
      return <Tag value={rowData.status} severity="danger" />;
    }
    return <Tag value={rowData.status} severity="success" />;
  };

  const renderHostNameBody = (rowData) => {
    const fieldValue = rowData.description || rowData.name;
    return <span>{fieldValue}</span>;
  };

  // const renderRestartServiceBody = (rowData) => (
  //   <Button icon="pi pi-undo" className="p-button-rounded p-button-primary"
  //   onClick={() => restartDevice(rowData)} disabled />
  // );

  const onRowCollapse = ({ data: hardware }) => {
    const hardwareIndex = hardwares.findIndex((item) => item.macAddreess === hardware.macAddreess);
    const newHardwaresList = [...hardwares];
    newHardwaresList[hardwareIndex].isRowOpen = false;
    setHardwares(newHardwaresList);
  };
  const onRowExpand = ({ data: hardware }) => {
    const hardwareIndex = hardwares.findIndex((item) => item.macAddreess === hardware.macAddreess);
    const newHardwaresList = [...hardwares];
    newHardwaresList[hardwareIndex].isRowOpen = true;
    setHardwares(newHardwaresList);
  };
  return (
    <DataTable
      value={hardwares}
      rowHover
      paginator
      rows={5}
      emptyMessage="Aucun appareil détecté"
      paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
      rowsPerPageOptions={[5, 10, 25]}
      expandedRows={expandedRows}
      onRowToggle={(e) => setExpandedRows(e.data)}
      onRowCollapse={(e) => onRowCollapse(e)}
      onRowExpand={(e) => onRowExpand(e)}
      rowExpansionTemplate={rowExpansionTemplate}
      dataKey="description"
    >
      <Column headerStyle={{ background: '#0d47a1' }} expander style={{ width: '2em' }} />
      <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} header="Nom de l'hôte" body={renderHostNameBody} bodyStyle={ColumnStyle.body} />
      <Column headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }} header="Statut" body={renderStatusBody} bodyStyle={ColumnStyle.body} />
      {/* <Column headerStyle={{ background: '#0d47a1',
      color: '#e8eaf6', textAlign: 'center' }} header="Redémarrer
      l'appareil" body={renderRestartServiceBody} bodyStyle={ColumnStyle.body} /> */}
    </DataTable>
  );
};

export default TableSettingsNetwork;
