import axios from 'axios';
import AbstractService from './abstractService';

export default class UserMethodProvider extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/users'));
    this.BASE_URL_AUTH_WS = process.env.REACT_APP_BASE_URL_WS;
  }

  /**
   * For user login
   */
  async connectUser(formValues) {
    try {
      const { username, password } = formValues;
      const response = await axios.post(`${this.BASE_URL_AUTH_WS}/login`, {
        username,
        password,
      });
      const { status } = response;
      const {
        uid, givenName, sn, groups,
      } = response.data.account;
      const { jws } = response.data;
      return {
        status,
        uid,
        givenName,
        sn,
        groups,
        jws,
        severity: 'success',
        summary: 'Succès',
        detail: 'Connexion réussie',
      };
    } catch (e) {
      if (e.response) {
        const { data, status, statusText } = e.response;
        return {
          severity: 'error', summary: 'Erreur', detail: data, status, message: statusText,
        };
      }
      const { message } = e;
      return {
        severity: 'error', summary: 'Erreur', message,
      };
    }
  }

  /**
   * Get all users.
   * @param {String}
   */
  async getUsers(token) {
    try {
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      const response = await axios.get(`${this.BASE_URL_AUTH_WS}/users`, { headers });
      const userList = [];
      response.data.forEach((user) => {
        if (user.entryDN.startsWith('uid')) {
          userList.push(user);
        }
      });
      return { status: response.status, users: userList };
    } catch (e) {
      if (e.response) {
        const { data, status, statusText } = e.response;
        return {
          severity: 'error', summary: 'Erreur', detail: data, status, message: statusText,
        };
      }
      const { message } = e;
      return {
        severity: 'error', summary: 'Erreur', message,
      };
    }
  }

  static firstLetterUpperCase(firstname) {
    if (firstname.length === 1) {
      return firstname.toUpperCase();
    }
    if (firstname.length === 0) {
      return firstname;
    }
    return firstname.substring(0, 0) + firstname[0].toUpperCase() + firstname.substring(1);
  }

  async createUser(token, formValues, uid) {
    try {
      const {
        lastname, firstname, password, roles,
      } = formValues;
      // const username = `${userFirstname.toLowerCase()}.${userLastname.toLowerCase()}`;
      const data = {
        cn: `${UserMethodProvider.firstLetterUpperCase(firstname)} ${lastname.toUpperCase()}`,
        createTimestamp: null,
        creatorsName: null,
        description: 'Real person.',
        entryCSN: null,
        entryDN: `uid=${uid},ou=persons,ou=users,ou=root,dc=ineo-es,dc=engie,dc=com`,
        entryUUID: null,
        gidNumber: 3000,
        hasSubordinates: false,
        homeDirectory: `/home/ineo-es/${uid}`,
        l: 'France', // Location
        loginShell: '/bin/bash',
        mail: `${uid}@ineo-es.engie.com`,
        modifiersName: null,
        modifyTimestamp: null,
        o: 'Ineo ES', // Organisation
        ou: 'groups',
        st: 'Occitanie',
        structuralObjectClass: null,
        subschemaSubentry: null,
        uid,
        uidNumber: 3000,
        givenName: firstname,
        sn: lastname.toUpperCase(),
        groups: roles,
        userPassword: password,
      };
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      const response = await axios.post(`${this.BASE_URL_AUTH_WS}/users`, data, {
        headers,
      });
      return { status: response.status, user: response.data };
    } catch (e) {
      if (e.response) {
        const { data, status, statusText } = e.response;
        return {
          severity: 'error', summary: 'Erreur', detail: data, status, message: statusText,
        };
      }
      const { message } = e;
      return {
        severity: 'error', summary: 'Erreur', message,
      };
    }
  }

  async deleteUser(token, uid) {
    try {
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      const response = await axios.delete(`${this.BASE_URL_AUTH_WS}/users/${uid}`, {
        headers,
      });
      return { status: response.status };
    } catch (e) {
      if (e.response) {
        const { data, status, statusText } = e.response;
        return {
          severity: 'error', summary: 'Erreur', detail: data, status, message: statusText,
        };
      }
      const { message } = e;
      return {
        severity: 'error', summary: 'Erreur', message,
      };
    }
  }

  async update(filters, identifier, user) {
    return new Promise((resolve, reject) => [
      super.update(filters, identifier, user)
        .then(() => {
          resolve();
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }
}
