/**
 * Class that represents an additional message.
 * @since 0.0.1
 * @author JACQUEL Romain
 */
class AdditionalMessage {
  constructor() {
    this.identifier = -1;
    this.date = new Date();
    this.isSynthetized = false;
    this.text = null;
    this.voice = null;
    this.isRecordedVoice = false;
    this.isMustBeBroadcasted = false;
    this.broadcastOrder = 0;
    this.audioFilePath = null;
    this.isBroadcasting = false;
  }

  /**
   * Function to convert a JSON to AdditionalMessage object.
   * @param {JSON} json AdditionalMessage as JSON object.
   */
  static parse(json) {
    console.log(json);
    if (json) {
      if (json.identifier !== undefined) {
        if (json.date !== undefined) {
          if (json.isSynthetized !== undefined) {
            if (json.text !== undefined) {
              if (json.voice !== undefined) {
                if (json.isRecordedVoice !== undefined) {
                  if (json.isMustBeBroadcasted !== undefined) {
                    if (json.broadcastOrder !== undefined) {
                      if (json.audioFilePath !== undefined) {
                        if (json.isBroadcasting !== undefined) {
                          const ADDITIONAL_MESSAGE = new AdditionalMessage();
                          ADDITIONAL_MESSAGE.identifier = json.identifier;
                          ADDITIONAL_MESSAGE.date = json.date;
                          ADDITIONAL_MESSAGE.isSynthetized = json.isSynthetized;
                          ADDITIONAL_MESSAGE.text = json.text;
                          ADDITIONAL_MESSAGE.voice = json.voice;
                          ADDITIONAL_MESSAGE.isRecordedVoice = json.isRecordedVoice;
                          ADDITIONAL_MESSAGE.isMustBeBroadcasted = json.isMustBeBroadcasted;
                          ADDITIONAL_MESSAGE.broadcastOrder = json.broadcastOrder;
                          ADDITIONAL_MESSAGE.audioFilePath = json.audioFilePath;
                          return ADDITIONAL_MESSAGE;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    return null;
  }
}

export default AdditionalMessage;
