import axios from 'axios';
import AbstractService from './abstractService';

export default class TextToSpeechMethodsProvider extends AbstractService {
  constructor(filename) {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/text-to-speech/download/').concat(filename));
  }

  async download(token) {
    try {
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      const response = await axios.get(this.url, { responseType: 'blob', headers });

      return { status: response.status, blob: response.data };
    } catch (error) {
      console.error(error);
      if (error.response) {
        return { status: error.response.status, message: error.response.statusText };
      }
      return { status: 500, message: error.message };
    }
  }
}
