import RtcLine from './RtcLine';

export default class RtcGateway {
  constructor() {
    this.rtcLines = [];
  }

  static compare(previous, next) {
    if (previous.port < next.port) {
      return -1;
    }
    if (previous.port > next.port) {
      return 1;
    }
    return 0;
  }

  static parse(json) {
    if (json) {
      if (json.rtcLines !== undefined) {
        const rtcGateway = new RtcGateway();
        if (json.rtcLines != null && Array.isArray(json.rtcLines)) {
          for (let index = 0; index < json.rtcLines.length; index += 1) {
            rtcGateway.rtcLines.push(RtcLine.parse(json.rtcLines[index]));
          }
          rtcGateway.rtcLines.sort(RtcGateway.compare);
        }
        return rtcGateway;
      }
    }
    return null;
  }
}
