/* eslint-disable prefer-destructuring */
/* eslint-disable max-len */
import { v4 as uuidv4 } from 'uuid';
import Broadcast from './Broadcast';
import textFormater from '../tools/text-formater';

/**
 * Class that represents a translation.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
class Translation {
  constructor() {
    this.identifier = null;
    this.bulletinIdentifier = null;
    this.bulletinName = null;
    this.language = null;
    this.decodedText = null;
    this.voice = null;
    this.vocalSynthesisDateStart = null;
    this.vocalSynthesisDateEnd = null;
    this.audioFilePath = null;
    this.broadcasts = [];
    this.status = null;
    this.error = null;
    this.audioFormat = null;
    this.frequency = null;
    this.isPlaying = false;
    this.isRowOpen = false;
    this.isBroadcasting = false;
  }

  isCurrentlyBroadcasting() {
    let isBroadcasting = false;
    for (let index = 0; index < this.broadcasts.length; index += 1) {
      if (this.broadcasts[index].isBroadcasting) {
        isBroadcasting = true;
      }
    }
    return isBroadcasting;
  }

  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.language !== undefined) {
          if (json.decodedText !== undefined) {
            if (json.voice !== undefined) {
              if (json.vocalSynthesisDateStart !== undefined) {
                if (json.audioFilePath !== undefined) {
                  if (json.status !== undefined) {
                    const translation = new Translation();
                    translation.identifier = json.identifier;
                    if (translation.identifier === 0) {
                      translation.identifier = uuidv4();
                    }
                    translation.bulletinIdentifier = json.bulletinIdentifier;
                    translation.bulletinName = json.bulletinName;
                    translation.language = json.language.toUpperCase();
                    translation.decodedText = textFormater(json.decodedText);
                    translation.voice = json.voice;
                    translation.vocalSynthesisDateStart = new Date(json.vocalSynthesisDateStart);
                    translation.vocalSynthesisDateEnd = new Date(json.vocalSynthesisDateEnd);
                    translation.audioFilePath = json.audioFilePath;
                    translation.isAudioFileExists = json.isAudioFileExists;
                    translation.status = json.status;
                    translation.error = json.error;
                    if (json.broadcasts != null && Array.isArray(json.broadcasts)) {
                      for (let index = 0; index < json.broadcasts.length; index += 1) {
                        translation.broadcasts.push(Broadcast.parse(json.broadcasts[index]));
                      }
                    }
                    translation.frequency = String(translation.voice.match(/\d+/g)).concat('k');
                    translation.voice = translation.voice.split(translation.frequency)[0];
                    const parts = translation.audioFilePath.split('.');
                    translation.audioFormat = parts[parts.length - 1];
                    translation.isBroadcasting = translation.isCurrentlyBroadcasting();
                    return translation;
                  }
                }
              }
            }
          }
        }
      }
    }
    return null;
  }
}

export default Translation;
