/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
/* eslint-disable max-len */
/* eslint-disable react/jsx-no-undef */
/* eslint-disable class-methods-use-this */
import React, {
  useCallback, useEffect, useState,
} from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route as BasicRoute,
} from 'react-router-dom';
import { useSelector } from 'react-redux';
import { Redirect, withRouter } from 'react-router';
import ErrorManager from './app/components/ErrorManager';
import WebsocketService from './services/websocketService';
import WebSocketClientEventListener from './tools/websocket/websocket-eventlistener';
import Header from './app/components/Header';
import Routes from './entities/Route';
import SideBar from './app/components/SideBar';
import BroadcastConfigurationPage from './app/pages/BroadcastConfigurationPage';
import ProtectedRoute from './app/components/ProtectedRoute';
import LoginPage from './app/pages/LoginPage';
import AccountManagerPage from './app/pages/AccountManagerPage';
import MonitoringPage from './app/pages/MonitoringPage';
import AnomaliesPage from './app/pages/AnomaliesPage';
import BulletinPage from './app/pages/BulletinPage';
import EventLogPage from './app/pages/EventLogPage';
import BroadcastMessagePage from './app/pages/BroadcastMessagePage';
import 'primereact/resources/themes/bootstrap4-light-blue/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';
import './App.scss';
import HistoryPage from './app/pages/HistoryPage';

function App({ location, history }) {
  class DefaultEventListener extends WebSocketClientEventListener {
    onOpen(event) {
      console.info(this, 'Websocket client is now connected.', event);
    }

    onError(event) {
      console.error(event);
    }

    onMessage() {
    }

    onClose(event) {
      console.info(this, 'Websocket client is now disconnected.', event);
    }
  }

  const [protectedRoutes, setProtectedRoutes] = useState(null);
  const [basicRoutes, setBasicRoutes] = useState(null);
  const userRolesFromState = useSelector((state) => state.user.roles);
  const alertsFromState = useSelector((state) => state.alerts);
  const userRolesFromLocalStorage = useState(JSON.parse(JSON.parse(localStorage.getItem('persist:root')).user).roles);
  const [routes, setRoutes] = useState([
    new Routes('Bulletins Météo', '/bulletins', BulletinPage, true, true, '/assets/images/icons/menu/home.svg', ['users', 'supervisors']),
    new Routes('Configuration de diffusion', '/broadcast-configuration', BroadcastConfigurationPage, true, true, '/assets/images/icons/menu/broadcast.svg', ['admins']),
    new Routes('Messages complémentaires', '/broadcast-message', BroadcastMessagePage, true, true, '/assets/images/icons/menu/audio.svg', ['users']),
    // new Routes('Tester diffusion VHF', '/test-broadcast', TestBroadcastPage, true, true, '/assets/images/icons/menu/test.svg', ['users', 'supervisors']),
    new Routes('Supervision', '/monitoring', MonitoringPage, true, true, '/assets/images/icons/menu/supervision.svg', ['users', 'supervisors']),
    new Routes('Statistiques', '/statistics', HistoryPage, true, true, '/assets/images/icons/menu/history.svg', ['users', 'supervisors']),
    new Routes('Anomalies', '/anomalies', AnomaliesPage, true, true, '/assets/images/icons/menu/warning.svg', ['users', 'supervisors'], alertsFromState.length),
    new Routes('Gestion de compte', '/manage-account', AccountManagerPage, true, true, '/assets/images/icons/menu/management.svg', ['admins']),
    new Routes('Journal d\'évènements', '/events-logs', EventLogPage, true, true, '/assets/images/icons/menu/event-log.svg', ['admins', 'users', 'supervisors']),
    new Routes('Déconnexion', '/login', LoginPage, true, true, '/assets/images/icons/menu/logout.svg'),
  ]);

  useEffect(() => {
    const websocket = WebsocketService.getInstance();
    websocket.setEventListener(new DefaultEventListener());
    websocket.connect();
  });

  const renderRoutes = () => routes.map((route) => {
    if (route.allowedGroups) {
      return route.allowedGroups.map((group) => {
        if (userRolesFromState !== null) {
          if (userRolesFromState.includes(group)) {
            return <ProtectedRoute exact={route.exact} path={route.path} component={route.component} allowedGroups={route.allowedGroups} />;
          }
        }
        return null;
      });
    }
    return <BasicRoute exact={route.exact} path={route.path} component={route.component} />;
  });

  return (
    <>
      <ErrorManager />
      <Header />
      <div className="dom-container">
        <div className="side-bar-container">
          <SideBar routes={routes} className="application-menu" />
        </div>
        <div className="components-container">
          <Switch>
            {renderRoutes()}
            <BasicRoute
              exact
              path="/"
              render={() => (
                <Redirect to="/bulletins" />
              )}
            />
          </Switch>
        </div>
      </div>
    </>
  );
}

export default withRouter(App);
