/**
 * Class that represents a runway.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
class Runway {
  constructor() {
    this.identifier = null;
    this.position = null;
    this.qfuThreshold1 = null;
    this.qfuThreshold2 = null;
    this.isLightingOn = false;
  }

  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.qfuThreshold1 !== undefined) {
          if (json.qfuThreshold2 !== undefined) {
            if (json.isLightingOn !== undefined) {
              const runway = new Runway();
              runway.identifier = json.identifier;
              runway.qfuThreshold1 = json.qfuThreshold1;
              runway.qfuThreshold2 = json.qfuThreshold2;
              runway.isLightingOn = json.isLightingOn;
              runway.position = json.position;
              return runway;
            }
          }
        }
      }
    }
    return null;
  }
}

export default Runway;
