/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, {
  useState, useEffect, useCallback,
} from 'react';
import { connect } from 'react-redux';
import { setError } from '../../../store/error/errorAction';
import AlertService from '../../../services/alertService';
import './styles.scss';
import AnomalyTable from '../../components/AnomalyTable';

const AnomaliesPage = ({ setErrorInState }) => {
  const [anomalies, setAnomalies] = useState(null);

  const getAlerts = useCallback(async () => {
    try {
      const alertService = new AlertService();
      const anomaliesList = await alertService.get({});
      setAnomalies(anomaliesList);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  }, [setErrorInState]);

  useEffect(() => {
    getAlerts();
  }, [getAlerts]);

  const AnomalyTableHeader = () => (
    <div className="table-header">
      <h3>Liste des alertes</h3>
    </div>
  );

  return (
    <main>
      <section>
        <AnomalyTable
          header={AnomalyTableHeader}
          alerts={anomalies}
        />
      </section>
    </main>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(AnomaliesPage);
