import AbstractService from './abstractService';

export default class SynthetizeMessageMethodsProvider extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/text-to-speech/synthetize'));
  }

  createSynthetizedMessage(messageText) {
    return new Promise((resolve, reject) => {
      super.create({}, messageText)
        .then((result) => {
          resolve(result);
        })
        .catch((e) => {
          reject(e);
        });
    });
  }
}
