export default class Software {
  constructor() {
    this.identifier = null;
    this.name = null;
    this.status = null;
    this.description = null;
  }

  static parse(json) {
    if (json) {
      // if (json.identifier !== undefined) {
      if (json.name !== undefined) {
        if (json.status !== undefined) {
          const software = new Software();
          // software.identifier = json.identifier;
          software.name = json.name;
          software.status = json.status;
          return software;
        }
      }
      // }
    }
    return null;
  }
}
