/* eslint-disable react/prop-types */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable no-unused-vars */
import React, {
  useState, useEffect, useRef, useCallback,
} from 'react';
import { Button } from 'primereact/button';
import { Chip } from 'primereact/chip';
import { Dialog } from 'primereact/dialog';
import { Password } from 'primereact/password';
import { useCookies } from 'react-cookie';
import { MultiSelect } from 'primereact/multiselect';
import { InputText } from 'primereact/inputtext';
import { Column } from 'primereact/column';
import { DataTable } from 'primereact/datatable';
import { Toast } from 'primereact/toast';
import { connect } from 'react-redux';
import { confirmPopup } from 'primereact/confirmpopup';
import { setError } from '../../../store/error/errorAction';
import RoleMethodsProvider from '../../../services/roleService';
import UserMethodProvider from '../../../services/userService';
import formValidation from '../../../tools/form-validation';
import './styles.scss';

const AccountManagerPage = ({ setErrorInState }) => {
  const [cookies, setCookie] = useCookies();
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [roles, setRoles] = useState(null);
  const [showUpdateModal, setShowUpdateModal] = useState(false);
  const [users, setUsers] = useState(null);
  const [globalFilter, setGlobalFilter] = useState(null);
  const [errorUsername, setErrorUsername] = useState(false);
  const [newPassword, setNewPassword] = useState({
    password: '',
    confirmPassword: '',
  });
  const [formUpdateUser, setFormUpdateUser] = useState(null);
  const [formValues, setFormValues] = useState({
    firstname: '', lastname: '', password: '', confirmPassword: '', roles: [],
  });
  const toast = useRef(null);
  const toastUser = useRef(null);

  const getRoles = useCallback(async () => {
    try {
      const RoleService = new RoleMethodsProvider();
      const rolesList = await RoleService.get({});
      setRoles(rolesList);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  }, [setErrorInState]);

  const getAllUsers = useCallback(async () => {
    try {
      const UserService = new UserMethodProvider();
      const response = await UserService.getUsers(cookies.jwt);

      setUsers(response.users);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  }, [cookies.jwt, setErrorInState]);

  useEffect(() => {
    getRoles();
    getAllUsers();
  }, [getAllUsers, getRoles]);

  const onHideUpdateModal = () => {
    setShowUpdateModal(false);
    setFormUpdateUser(null);
    setNewPassword({
      password: '',
      confirmPassword: '',
    });
  };

  const onHideCreateModal = () => {
    setShowCreateModal(false);
    setFormValues({
      firstname: '', lastname: '', password: '', confirmPassword: '', roles: [],
    });
  };

  const updateFormUpdateUser = (e) => {
    setFormUpdateUser({
      ...formUpdateUser,
      [e.target.name]: e.target.value,
    });
  };

  const updateFormValuesForCreation = (e) => {
    setFormValues({
      ...formValues,
      [e.target.name]: e.target.value || e.value,
    });
  };

  const updateFormValues = (e) => {
    setFormUpdateUser({
      ...formUpdateUser,
      groups: [...e.value],
    });
  };

  const updateNewPassword = (e) => {
    setNewPassword({
      ...newPassword,
      [e.target.name]: e.target.value,
    });
  };

  const normalizeNames = (name) => {
    if (name.length > 1) {
      let identifiant = name;
      // Supprime les accents
      identifiant = identifiant.normalize('NFD').replace(/[\u0300-\u036f]/g, '');

      // Supprime les espaces
      identifiant = identifiant.replace(' ', '_').toLowerCase();
      console.log(identifiant);
      return identifiant;
    }
    return '';
  };

  const createUser = async () => {
    try {
      const errors = await formValidation(formValues);

      if (errors.length === 0) {
        const UserService = new UserMethodProvider();
        const response = await UserService.createUser(
          cookies.jwt,
          formValues,
          normalizeNames(`${formValues.firstname}.${formValues.lastname}`),
        );

        if (response.status === 200) {
          setUsers([...users, response.user]);
          setShowCreateModal(false);
          toastUser.current.show({
            severity: 'success', summary: 'Message de succès', detail: 'Le compte à bien été crée', life: 3000,
          });
          setFormValues({
            firstname: '', lastname: '', password: '', confirmPassword: '', roles: [],
          });
        } else {
          toastUser.current.show({
            severity: 'error', summary: "Message d'erreur", detail: response.detail, life: 5000,
          });
        }
      } else {
        toast.current.show({
          severity: 'error',
          summary: 'Erreur formulaire',
          life: 10000,
          content: (
            <div className="errors-container">
              <h4>
                Erreur
                {errors.length > 1 ? 's' : ''}
                {' '}
                dans le formulaire
              </h4>
              <ul>
                {errors.map((err) => (
                  <li>{err}</li>
                ))}
              </ul>
            </div>
          ),
        });
      }
    } catch (err) {
      setErrorInState(err.response ? err.response.status : err.status || 500);
    }
  };

  const renderFooterCreateUser = () => (
    <div>
      <Button label="Annuler" icon="pi pi-times" onClick={() => onHideCreateModal()} className="p-button-text" />
      <Button label="Ajouter" icon="pi pi-check" autoFocus onClick={(e) => createUser(e)} />
    </div>
  );

  const renderFooterUpdateUser = () => (
    <div>
      <Button label="Annuler" icon="pi pi-times" onClick={() => onHideUpdateModal()} className="p-button-text" />
      <Button label="Confirmer" icon="pi pi-check" autoFocus onClick={(e) => updateUser(e)} />
    </div>
  );

  const updateUser = async () => {
    try {
      if (newPassword.password !== '') {
        if (newPassword.password === newPassword.confirmPassword) {
          const formToSend = { ...formUpdateUser };
          formToSend.userPassword = newPassword.password;

          const UserService = new UserMethodProvider();
          await UserService.update({}, formToSend.uid, formToSend);
          const newUsersList = [...users];
          const userIndex = newUsersList.findIndex((user) => user.uid === formToSend.uid);
          newUsersList[userIndex] = { ...formToSend };
          setUsers([...newUsersList]);
          setShowUpdateModal(false);
          toast.current.show({
            severity: 'success', summary: 'Message de succès', detail: 'Le compte à bien été modifié', life: 3000,
          });
        } else {
          toast.current.show({
            severity: 'error', summary: 'Message d\'erreur', detail: 'Veuillez saisir deux mots de passe identiques', life: 3000,
          });
        }
      } else {
        const UserService = new UserMethodProvider();

        await UserService.update({}, formUpdateUser.uid, formUpdateUser);
        const newUsersList = [...users];
        const userIndex = newUsersList.findIndex((user) => user.uid === formUpdateUser.uid);
        newUsersList[userIndex] = { ...formUpdateUser };
        setUsers([...newUsersList]);
        setShowUpdateModal(false);
      }
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  };

  const deleteUser = async (e) => {
    try {
      const UserService = new UserMethodProvider();
      const uid = e;
      await UserService.deleteUser(cookies.jwt, uid);

      const newUserArray = [...users];
      const findUserIndex = newUserArray.findIndex((user) => user.uid === uid);
      newUserArray.splice(findUserIndex, 1);
      setUsers(newUserArray);
    } catch (err) {
      setErrorInState(err.response ? err.response.status : err.status || 500);
    }
  };

  const openUpdateUserDialog = (item) => {
    const userIndex = users.findIndex((user) => user.uid === item.uid);
    setFormUpdateUser({
      ...users[userIndex],
    });
    setShowUpdateModal(true);
  };

  const bodyUpdateUser = (item) => (
    <Button
      className="p-button-rounded p-button-outlined"
      icon="pi pi-user-edit"
      onClick={() => openUpdateUserDialog(item)}
    />
  );

  const bodyRole = (account) => {
    if (account.groups) {
      if (account.groups.length > 0) {
        return account.groups.map((role) => <Chip key={role} label={role} className={`role-${role}`} />);
      }
    }
    return '';
  };

  const accept = (uid) => {
    if (uid) {
      deleteUser(uid);
      toast.current.show({
        severity: 'success', summary: 'Message de succès', detail: 'Le compte à bien été supprimé', life: 3000,
      });
    }
  };

  const reject = () => {
    toast.current.show({
      severity: 'info', summary: 'Annulé', detail: 'Opération annulée', life: 3000,
    });
  };

  const confirmDeletion = (event, uid) => {
    // selectUserThatCanBeDeleted(uid);
    confirmPopup({
      target: event.currentTarget,
      message: 'Voulez-vous supprimer cet utilisateur ?',
      icon: 'pi pi-info-circle',
      acceptClassName: 'p-button-danger',
      accept: () => accept(uid),
      reject,
    });
  };

  const bodyDelete = (item) => (
    <Button onClick={(e) => confirmDeletion(e, item.uid)} icon="pi pi-trash" className="p-button-danger p-button-rounded p-button-outlined" />
  );

  const AccountTableHeader = () => (
    <div className="table-header">
      <h3>Liste des comptes</h3>
    </div>
  );

  return (
    <>
      <Toast ref={toast} baseZIndex={10000} />
      <Toast ref={toastUser} baseZIndex={10000} />
      <main>
        <section className="monitoring-accounts-manager-container">
          <Button label="Ajouter un compte" style={{ marginBottom: '1rem' }} icon="pi pi-plus" onClick={() => setShowCreateModal(true)} className="p-button-success p-mr-2" />
          <Dialog className="add-account-dialog" header="Ajouter un compte" visible={showCreateModal} footer={() => renderFooterCreateUser()} onHide={() => onHideCreateModal()}>
            <div className="role-dropdown-container">
              <h6>Rôles</h6>
              <MultiSelect
                name="roles"
                value={formValues.roles}
                options={roles}
                onChange={(e) => updateFormValuesForCreation(e)}
                placeholder="Séléctionnez le rôle de l'utilisateur"
              />
            </div>
            <div className="firstname-input-container">
              <h6>Prénom</h6>
              <InputText name="firstname" onChange={(e) => updateFormValuesForCreation(e)} />
            </div>
            <div className="lastname-input-container">
              <h6>Nom</h6>
              <InputText name="lastname" onChange={(e) => updateFormValuesForCreation(e)} />
            </div>
            <div>
              <h6>Votre identifiant de connexion</h6>
              <p>
                {normalizeNames(`${formValues.firstname}.${formValues.lastname}`)}
              </p>
            </div>
            <div className="password-input-container">
              <h6>Mot de passe</h6>
              <Password
                value={formValues.password}
                name="password"
                feedback
                onChange={(e) => updateFormValuesForCreation(e)}
                toggleMask
              />
            </div>
            <div className="confirm-password-input-container">
              <h6>Confirmation du mot de passe</h6>
              <Password
                value={formValues.confirmPassword}
                name="confirmPassword"
                feedback={false}
                onChange={(e) => updateFormValuesForCreation(e)}
                toggleMask
              />
            </div>
          </Dialog>
          <Dialog className="update-account-dialog" header="Modifier un compte" visible={showUpdateModal} footer={() => renderFooterUpdateUser()} onHide={() => onHideUpdateModal()}>
            <div className="role-dropdown-container">
              <h6>
                Rôles
              </h6>
              <MultiSelect
                name="groups"
                value={formUpdateUser !== null && formUpdateUser.groups}
                options={roles}
                onChange={(e) => updateFormValues(e)}
                placeholder="Séléctionnez le rôle de l'utilisateur"
              />
            </div>
            <div className="password-input-container">
              <h6>Nouveau mot de passe</h6>
              <Password
                value={newPassword.password}
                name="password"
                feedback
                onChange={(e) => updateNewPassword(e)}
                toggleMask
              />
            </div>
            <div className="confirm-password-input-container">
              <h6>Confirmation du nouveau mot de passe</h6>
              <Password
                value={newPassword.confirmPassword}
                name="confirmPassword"
                feedback={false}
                onChange={(e) => updateNewPassword(e)}
                toggleMask
              />
            </div>
          </Dialog>
          <DataTable
            paginator
            rows={5}
            header={AccountTableHeader()}
            value={users}
          // header={header}
            rowsPerPageOptions={[5, 10, 20]}
            paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
            globalFilter={globalFilter}
          >
            <Column
              headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }}
              style={{ textAlign: 'center' }}
              field="cn"
              header="Prénom et NOM"
            />
            <Column
              headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }}
              style={{ textAlign: 'center' }}
              field="uid"
              header="Identifiant"
            />
            <Column
              headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }}
              style={{ textAlign: 'center' }}
              body={bodyRole}
              header="Rôles"
            />
            <Column
              headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }}
              style={{ textAlign: 'center' }}
              body={bodyUpdateUser}
              header="Modifier"
            />
            <Column
              headerStyle={{ background: '#0d47a1', color: '#e8eaf6', textAlign: 'center' }}
              style={{ textAlign: 'center' }}
              body={bodyDelete}
              field="delete"
              header="Supprimer"
            />
          </DataTable>
        </section>
      </main>
    </>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});
export default connect(null, mapDispatchToProps)(AccountManagerPage);
