/* eslint-disable react/prop-types */
import React, { useCallback, useEffect, useRef } from 'react';
import { withRouter } from 'react-router';
import { connect, useSelector } from 'react-redux';
import { Toast } from 'primereact/toast';
import { resetError } from '../../../store/error/errorAction';
import { resetUser } from '../../../store/user/userAction';

const ErrorManager = ({ resetErrorInState, resetUserInState, history }) => {
  const toast = useRef(null);
  const requestStatusFromState = useSelector((state) => state.error.requestStatus);

  const manageError = useCallback(async () => {
    if (requestStatusFromState === 401) {
      toast.current.show({ severity: 'error', summary: "Message d'erreur", detail: 'Votre session à expiré, veuillez vous authentifier' });
      resetUserInState();
      history.push('/login');
    } else if (requestStatusFromState === 403) {
      toast.current.show({ severity: 'error', summary: "Message d'erreur", detail: 'Accès non authorisé' });
      history.push('/bulletins');
    } else if (requestStatusFromState === 404) {
      toast.current.show({ severity: 'error', summary: "Message d'erreur", detail: 'La ressource demandée est introuvable' });
      history.push('/bulletins');
    } else if (requestStatusFromState !== null) {
      toast.current.show({ severity: 'error', summary: "Message d'erreur", detail: 'Erreur serveur, veuillez vérifier le bon fonctionnement de votre connexion internet' });
    }
    resetErrorInState();
  }, [requestStatusFromState, resetErrorInState, resetUserInState, history]);

  useEffect(() => {
    manageError();
  }, [manageError]);

  return (
    <Toast ref={toast} baseZIndex={10000} />
  );
};
// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  resetErrorInState: () => dispatch(resetError()),
  resetUserInState: () => dispatch(resetUser()),
});

export default withRouter(connect(null, mapDispatchToProps)(ErrorManager));
