/* eslint-disable max-len */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, {
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useCookies } from 'react-cookie';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { InputNumber } from 'primereact/inputnumber';
import { connect } from 'react-redux';
import { InputSwitch } from 'primereact/inputswitch';
import { setError } from '../../../store/error/errorAction';
// import { compareCurrentAndTotalTime } from '../../../tools/player';
import UtcDate from '../../../entities/date';
import AdditionalMessageService from '../../../services/additionalMessageService';
import './styles.scss';

const TableAdditionalMessage = ({
  audio, setAudioURL, additionalsMessages, setAdditionalsMessages, setErrorInState,
}) => {
  const ADDITIONAL_MESSAGE_SERVICE = useMemo(() => new AdditionalMessageService(), []);
  const [cookies] = useCookies();

  // eslint-disable-next-line consistent-return
  const updateValuesManager = (e, type, additionalMessage) => {
    const newAdditionalsMessagesList = [...additionalsMessages];
    const additionalMessageIndex = newAdditionalsMessagesList.findIndex((element) => element.identifier === additionalMessage.identifier);

    switch (type) {
      case 'update-broadcast-order':
        newAdditionalsMessagesList[additionalMessageIndex].broadcastOrder = e.value;
        break;
      case 'update-broadcast-status':
        newAdditionalsMessagesList[additionalMessageIndex].isMustBeBroadcasted = !newAdditionalsMessagesList[additionalMessageIndex].isMustBeBroadcasted;
        console.log(newAdditionalsMessagesList[additionalMessageIndex].isMustBeBroadcasted);
        break;
      default:
        console.log('updateValuesManager() => Case is not implemented');
        return;
    }

    ADDITIONAL_MESSAGE_SERVICE.update(newAdditionalsMessagesList[additionalMessageIndex])
      .then((response) => {
        console.log(response);
        setAdditionalsMessages(newAdditionalsMessagesList);
      })
      .catch((err) => {
        setErrorInState(err.response ? err.response.status : err.status || 500);
      });
  };

  const playPause = (e, additionalMessage) => {
    const { identifier, isBroadcasting } = additionalMessage;
    const newAdditionalsMessagesList = [...additionalsMessages];

    const currentAdditionalMessageIndex = newAdditionalsMessagesList.findIndex((element) => element.identifier === identifier);

    // 1 - Si on clique sur le meme audio qu'au coup précédent
    if (audio.additionalMessageIdentifier === identifier) {
      if (isBroadcasting) {
        newAdditionalsMessagesList[currentAdditionalMessageIndex].isBroadcasting = false;
        audio.pause();
      } else {
        newAdditionalsMessagesList[currentAdditionalMessageIndex].isBroadcasting = true;
        audio.play();
      }
    } else {
      // 2 - Si on clique pas sur le meme audio qu'au coup précendent
      // => On passe tous les isBroadcasting à false
      newAdditionalsMessagesList.forEach((message) => {
        if (message.isBroadcasting) {
          message.isBroadcasting = false;
        }
      });

      // 3 - On fait / refait notre requête à l'API.
      ADDITIONAL_MESSAGE_SERVICE.download(cookies.jwt, identifier)
        .then((response) => {
          console.log(response);
          // On marque notre objet audio avec l'identifier de l'additional message qu'il lit.
          audio.additionalMessageIdentifier = identifier;

          /* On modifie la propriété isBroadcast de notre additionalMessage pour indiquer
          que nous somme en lecture. */
          newAdditionalsMessagesList[currentAdditionalMessageIndex].isBroadcasting = true;

          // On créer notre blob url
          const blobURL = URL.createObjectURL(response.blob);

          // On set notre audio avec la blob url créée
          setAudioURL(blobURL);
          audio.play();
        })
        .catch((err) => {
          setErrorInState(err.response ? err.response.status : err.status || 500);
        });
    }
    setAdditionalsMessages(newAdditionalsMessagesList);
  };

  const TableAdditionalMessageBodyManager = (item, type) => {
    switch (type) {
      case 'date':
        return (
          <span>
            {new UtcDate(new Date(item.date)).format(process.env.REACT_APP_DATE_FORMAT)}
          </span>
        );
      case 'audio-type': {
        if (item.isRecordedVoice) return 'Message vocal';
        return 'Message synthétisé';
      }
      case 'broadcast-order': {
        return (
          <InputNumber
            inputId="integeronly"
            value={item.broadcastOrder}
            onValueChange={(e) => updateValuesManager(e, 'update-broadcast-order', item)}
            min={1}
            max={additionalsMessages && additionalsMessages.length}
            showButtons
            buttonLayout="vertical"
          />
        );
      }
      case 'player': {
        if (item.isBroadcasting) {
          return (
            <i
              onClick={(e) => playPause(e, item)}
              className="pi pi-pause"
            />
          );
        }
        return (
          <i
            onClick={(e) => playPause(e, item)}
            className="pi pi-play"
          />
        );
      }

      case 'broadcast-status': {
        return (
          <InputSwitch
            checked={item.isMustBeBroadcasted}
            onChange={(e) => updateValuesManager(e, 'update-broadcast-status', item)}
          />
        );
      }

      default:
        console.log('TableAdditionalMessageBodyManager() => Case is not implemented');
        return null;
    }
  };

  return (
    <div className="table-additional-message-container">
      <DataTable
        value={additionalsMessages}
        emptyMessage="No additionnal message found"
        paginator
        rows={10}
        rowsPerPageOptions={[10, 30, 60]}
        paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
      >
        <Column style={{ textAlign: 'center' }} body={(item) => TableAdditionalMessageBodyManager(item, 'player')} header="Lecture" />
        <Column style={{ textAlign: 'center' }} body={(item) => TableAdditionalMessageBodyManager(item, 'date')} header="Date de création" />
        <Column style={{ textAlign: 'center' }} body={(item) => TableAdditionalMessageBodyManager(item, 'audio-type')} header="Type d'audio" />
        <Column style={{ textAlign: 'center' }} body={(item) => TableAdditionalMessageBodyManager(item, 'broadcast-order')} header="Order de diffusion" />
        <Column style={{ textAlign: 'center' }} body={(item) => TableAdditionalMessageBodyManager(item, 'broadcast-status')} header="Activer / désactiver diffusion" />
      </DataTable>
    </div>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(TableAdditionalMessage);
