function formValidation(formValues) {
  return new Promise((resolve, reject) => {
    const errors = [];
    let password = null;

    if (!formValues) {
      reject(new Error('formValues parameter should be defined'));
    }

    Object.entries(formValues).forEach((field) => {
      const fieldName = field[0];
      const fieldValue = field[1];

      if (!fieldValue) {
        errors.push(`${fieldName} ne peux pas être vide`);
      }
      if (fieldName === 'password') {
        password = fieldValue;
      }
      if (fieldName === 'confirmPassword' && password !== fieldValue) {
        errors.push('Les mots de passe doivent être similaires');
      }
      if (fieldName === 'roles' && fieldValue.length === 0) {
        errors.push("Des roles doivent être définis pour l'utilisateur");
      }
    });
    resolve(errors);
  });
}

export default formValidation;
