export default class Vhf {
  constructor() {
    this.identifier = null;
    this.inputblock = 0;
    this.hardware = null;
    this.component = null;
    this.mode = null;
    this.isTransmitting = false;
    this.isReceiving = false;
  }

  static parse(json) {
    if (json) {
      if (json.inputblock !== undefined) {
        if (json.hardware !== undefined) {
          if (json.component !== undefined) {
            if (json.mode !== undefined) {
              const vhf = new Vhf();
              vhf.identifier = json.identifier;
              vhf.inputblock = json.inputblock;
              vhf.hardware = json.hardware;
              vhf.component = json.component;
              vhf.mode = json.mode;
              vhf.isTransmitting = json.isTransmitting;
              vhf.isReceiving = json.isReceiving;
              return vhf;
            }
          }
        }
      }
    }
    return null;
  }
}
