export default class RtcLine {
  constructor() {
    this.identifier = null;
    this.isBroadcasting = null;
    this.isCidRestricted = null;
    this.phoneNumber = null;
    this.port = null;
    this.portType = null;
    this.status = null;
  }

  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.isBroadcasting !== undefined) {
          if (json.isCidRestricted !== undefined) {
            if (json.phoneNumber !== undefined) {
              if (json.port !== undefined) {
                if (json.portType !== undefined) {
                  if (json.status !== undefined) {
                    const rtcLine = new RtcLine();
                    rtcLine.identifier = json.identifier;
                    rtcLine.isBroadcasting = json.isBroadcasting;
                    rtcLine.isCidRestricted = json.isCidRestricted;
                    rtcLine.phoneNumber = json.phoneNumber;
                    rtcLine.port = json.port;
                    rtcLine.portType = json.portType;
                    rtcLine.status = json.status;
                    return rtcLine;
                  }
                }
              }
            }
          }
        }
      }
    }
    return null;
  }
}
