/* eslint-disable class-methods-use-this */
/**
 * Class that represents a WebSocket Client EventListener.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
export default class WebSocketClientEventListener {
  static getJson(string) {
    try {
      return JSON.parse(string);
    } catch (error) {
      return null;
    }
  }

  /**
   * Function called when connection is opned.
   * @param {Event} event WebSocket event when websocket connection is opened.
   */
  onOpen(event) {
    throw new Error('Unimplemented. Event to trigged: ', this, event);
  }

  /**
   * Function called when an error is detected.
   * @param {Event} event WebSocket event when websocket is failed.
   */
  onError(event) {
    throw new Error('Unimplemented. Event to trigged: ', this, event);
  }

  /**
   * Function called when an message is detected.
   * @param {MessageEvent<any>} event WebSocket event when new message.
   */
  onMessage(event) {
    throw new Error('Unimplemented. Event to trigged: ', this, event);
  }

  /**
   * Function called when connection is closed.
   * @param {CloseEvent} event WebSocket event when websocket connection is cloed.
   */
  onClose(event) {
    throw new Error('Unimplemented. Event to trigged: ', this, event);
  }
}
