/**
 * Class that represents an airport.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
export default class StorageDisk {
  constructor() {
    this.identifier = null;
    this.name = null;
    this.used = null;
    this.free = null;
    this.total = null;
    this.measure = null;
    this.usagePercent = null;
  }

  /**
   * Function to convert a JSON to Airport object.
   * @param {JSON} json Airport as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.name !== undefined) {
          if (json.used !== undefined) {
            if (json.free !== undefined) {
              if (json.total !== undefined) {
                if (json.measure !== undefined) {
                  const storageDisk = new StorageDisk();
                  storageDisk.identifier = json.identifier;
                  storageDisk.name = json.name;
                  storageDisk.used = json.used;
                  storageDisk.free = json.free;
                  storageDisk.total = json.total;
                  storageDisk.measure = json.measure;
                  storageDisk.usagePercent = json.usagePercent;
                  return storageDisk;
                }
              }
            }
          }
        }
      }
    }
    return null;
  }
}
