/* eslint-disable no-nested-ternary */
/**
 * Class that represents an UTC Date.
 */
export default class UtcDate {
  /**
   * UTC Date Constructor that take an optional date timestamp.
   */
  constructor(origin = new Date()) {
    /**
     * Year
     */
    this.year = origin.getUTCFullYear();

    /**
     * Month number.
     */
    this.month = origin.getUTCMonth() + 1;

    /**
     * Day of the month.
     */
    this.date = origin.getUTCDate();

    /**
     * Day of the week.
     */
    this.day = origin.getUTCDay();

    /**
     * Hour
     */
    this.hours = origin.getUTCHours();

    /**
     * Minute
     */
    this.minutes = origin.getUTCMinutes();

    /**
     * Seconds.
     */
    this.seconds = origin.getUTCSeconds();

    /**
     * Milliseconds.
     */
    this.milliseconds = origin.getUTCMilliseconds();
  }

  /**
   * Format the date in given format.
   * @param {String} pattern Format of the date. (ex: yyyy-MM-dd hh:mm:ss:SSS UTC)
   * @returns Formated date as string.
   */
  format(pattern) {
    let result = String(pattern);
    result = result.replace('yyyy', this.year);
    result = result.replace('y', String(this.year).substr(0, 2));
    result = result.replace('MM', String((this.month.toString().length === 1) ? String('0').concat(this.month.toString()) : this.month));
    result = result.replace('M', String(this.month));
    result = result.replace('dd', String((this.date.toString().length === 1) ? String('0').concat(this.date.toString()) : this.date));
    result = result.replace('d', String(this.date));
    result = result.replace('FF', String((this.day.toString().length === 1) ? String('0').concat(this.day.toString()) : this.day));
    result = result.replace('F', String(this.day));
    result = result.replace('hh', String((this.hours.toString().length === 1) ? String('0').concat(this.hours.toString()) : this.hours));
    result = result.replace('h', String(this.hours));
    result = result.replace('mm', String((this.minutes.toString().length === 1) ? String('0').concat(this.minutes.toString()) : this.minutes));
    result = result.replace('m', String(this.minutes));
    result = result.replace('ss', String((this.seconds.toString().length === 1) ? String('0').concat(this.seconds.toString()) : this.seconds));
    result = result.replace('s', String(this.seconds));
    result = result.replace('SSS', String((this.milliseconds.toString().length === 1) ? String('00').concat(this.milliseconds.toString()) : (this.milliseconds.toString().length === 2) ? String('0').concat(this.milliseconds.toString()) : this.milliseconds));
    result = result.replace('S', String(this.milliseconds));
    return result;
  }
}
