import { CREATE_ALERT, RESET_ALERTS } from './alertActionType';

const initialState = [];

export default function alertReducer(state = initialState, action) {
  switch (action.type) {
    case CREATE_ALERT:
      state.unshift(action.data);
      return [...state];
    case RESET_ALERTS:
      state = initialState;
      return [...state];
    default:
      return state;
  }
}
