/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, { useMemo, useRef, useState } from 'react';
import { InputTextarea } from 'primereact/inputtextarea';
import { Button } from 'primereact/button';
import { connect } from 'react-redux';
import { useCookies } from 'react-cookie';
import { Toast } from 'primereact/toast';
import { setError } from '../../../store/error/errorAction';
import AdditionalMessageService from '../../../services/additionalMessageService';
import SynthetizeMessageMethodsProvider from '../../../services/synthetizeMessageService';
import TextToSpeechMethodsProvider from '../../../services/textToSpeechService';
import AdditionalMessage from '../../../entities/additionalMessage';
import SynthetizeMessage from '../../../entities/synthetizeMessage';
import './styles.scss';

const SynthesisMessageCreation = ({
  setErrorInState, setAdditionalsMessages,
}) => {
  const ADDITIONAL_MESSAGE_SERVICE = useMemo(() => new AdditionalMessageService(), []);
  const ADDITIONAL_MESSAGE = useMemo(() => new AdditionalMessage(), []);
  const [text, setText] = useState('');
  const [blobURL, setBlobURL] = useState(null);
  const [blob, setBlob] = useState(null);
  const [filename, setFilename] = useState(null);
  const [messageHasBeenListened, setMessageHasBeenListened] = useState(false);
  const [cookies] = useCookies();
  const toast = useRef(null);
  const audioElement = document.getElementsByClassName('audio-element-message-synthetize')[0];

  const createSynthetisedMessage = async () => {
    try {
      const SynthetiseMessageService = new SynthetizeMessageMethodsProvider();
      const messageToSynthetize = new SynthetizeMessage(text);

      const response = await SynthetiseMessageService.createSynthetizedMessage(messageToSynthetize);
      setFilename(response);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  };

  const audioManager = async () => {
    try {
      if (filename) {
        const TextToSpeechService = new TextToSpeechMethodsProvider(filename);
        const response = await TextToSpeechService.download(cookies.jwt);
        setBlob({ blob: response.blob, filename });
        const blobUrl = URL.createObjectURL(response.blob);
        setBlobURL(blobUrl);
        console.log({ audioElement });
        audioElement.play();
        setMessageHasBeenListened(true);
      }
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  };

  const submitAudio = () => {
    const formData = new FormData();
    ADDITIONAL_MESSAGE.text = text;
    ADDITIONAL_MESSAGE.isSynthetized = true;
    console.log(blob);
    formData.append('file', blob.blob, blob.filename);
    formData.append('additional-message', ADDITIONAL_MESSAGE);
    ADDITIONAL_MESSAGE_SERVICE.create(formData).then((response) => {
      console.log(response);
      setText(null);
      setFilename(null);
      setMessageHasBeenListened(false);
      setAdditionalsMessages((messages) => [...messages, response]);
      toast.current.show({ severity: 'success', summary: 'Message de succès', detail: 'Message enregistré' });
    }).catch((e) => {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    });
  };

  const resetState = () => {
    setFilename(null);
    setText(null);
    setMessageHasBeenListened(false);
  };

  const cancel = () => {
    resetState();
    toast.current.show({ severity: 'info', summary: "Message d'information", detail: 'Processus annulé' });
  };

  const renderTextarea = () => {
    if (!filename) {
      return (
        <InputTextarea
          className="synthesis-message-textarea"
          value={text}
          onChange={(e) => setText(e.target.value)}
          autoResize
        />
      );
    }
    return null;
  };

  const renderButtons = () => {
    if (!filename) {
      return (
        <div className="button-container">
          <Button
            className="p-button-primary"
            icon="pi pi-check"
            label="Synthétiser ce message"
            onClick={() => createSynthetisedMessage()}
          />
        </div>
      );
    }
    return (
      <div className="buttons-container">
        <Button
          className="p-button-secondary"
          icon="pi pi-play"
          label="Ecouter"
          onClick={() => audioManager()}
        />
        <Button
          className="p-button-success"
          icon="pi pi-check"
          label="Valider"
          disabled={!messageHasBeenListened}
          onClick={() => submitAudio()}
        />
        <Button
          className="p-button-danger"
          icon="pi pi-times"
          label="Annuler"
          onClick={() => cancel()}
        />
      </div>
    );
  };

  return (
    <div className="synthesis-message-container">
      <Toast ref={toast} baseZIndex={10000} />
      <audio
        className="audio-element-message-synthetize"
        src={blobURL && blobURL}
        type="audio/webm;codecs=opus"
      />
      {renderTextarea()}
      {renderButtons()}
    </div>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(SynthesisMessageCreation);
