import axios from 'axios';
import EventLog from '../entities/eventLog';
import AbstractService from './abstractService';

export default class EventLogService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/event-log'));
  }

  get(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const eventLogs = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              eventLogs.push(EventLog.parse(json[index]));
            }
          } else {
            eventLogs.push(EventLog.parse(json));
          }
          resolve(eventLogs);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  getOldEventLog(month, filename, filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const eventLogs = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              eventLogs.push(EventLog.parse(json[index]));
            }
          } else {
            eventLogs.push(EventLog.parse(json));
          }
          resolve(eventLogs);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  download(month, filename, filters) {
    return new Promise((resolve, reject) => {
      let urlForDownload = String(this.url).concat('/download');
      if (month && filename) {
        urlForDownload = String(urlForDownload).concat(`/${month}/${filename}`);
      }
      axios.get(
        urlForDownload,
        { headers: this.headers, params: filters },
        { responseType: 'blob' },
      )
        .then((httpResponse) => {
          const { data, headers } = httpResponse;
          const fileName = headers['File-Name'] || filename || `ride4ever-ws-${new Date().toJSON()}.log`;
          const blob = new Blob([data], { type: headers['content-type'] });
          const dom = document.createElement('a');
          const url = window.URL.createObjectURL(blob);
          dom.href = url;
          dom.download = decodeURI(fileName);
          dom.style.display = 'none';
          document.body.appendChild(dom);
          dom.click();
          dom.parentNode.removeChild(dom);
          window.URL.revokeObjectURL(url);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }
}
