/**
 * Class that represents a localization.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
class NetworkCard {
  constructor() {
    this.macAddreess = '';
    this.ipv4Address = '';
    this.netmask = 0;
    this.dns = [];
    this.gateway = '';
    this.ipv6Address = '';
  }

  static parse(json) {
    if (json) {
      if (json.macAddreess !== undefined) {
        if (json.ipv4Address !== undefined) {
          if (json.netmask !== undefined) {
            if (json.dns !== undefined) {
              if (json.gateway !== undefined) {
                if (json.ipv6Address !== undefined) {
                  const entity = new NetworkCard();
                  entity.macAddreess = json.macAddreess;
                  entity.ipv4Address = json.ipv4Address;
                  entity.netmask = json.netmask;
                  entity.dns = json.dns;
                  entity.gateway = json.gateway;
                  entity.ipv6Address = json.ipv6Address;
                  return entity;
                }
              }
            }
          }
        }
      }
    }
    return null;
  }
}

export default NetworkCard;
