/* eslint-disable react/prop-types */
import React, { useState } from 'react';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { ProgressSpinner } from 'primereact/progressspinner';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { useCookies } from 'react-cookie';
import { setToast, resetToast } from '../../../store/toast/toastAction';
import { setUser } from '../../../store/user/userAction';
import { setError } from '../../../store/error/errorAction';
import UserMethodProvider from '../../../services/userService';
import './styles.scss';

// eslint-disable-next-line no-unused-vars
const LoginPage = ({
  setUserInStore, history, setToastInStore, resetToastInStore, setErrorInState,
}) => {
  const [formValues, setFormValues] = useState({ username: '', password: '' });
  // eslint-disable-next-line no-unused-vars
  const [cookies, setCookie] = useCookies();
  const [isLoading, setIsLoading] = useState(false);
  const UserService = new UserMethodProvider();

  const handleSubmit = async (e) => {
    setIsLoading(true);
    try {
      e.preventDefault();
      const response = await UserService.connectUser(formValues);

      if (response.status === 200) {
        setCookie('jwt', response.jws);
        setUserInStore({
          username: response.uid,
          firstname: response.givenName,
          lastname: response.sn,
          roles: response.groups,
          jwt: response.jws,
        });
        setToastInStore({
          severity: response.severity,
          summary: response.summary,
          detail: response.detail,
        });
        resetToastInStore();
        history.push('/bulletins');
      } else {
        setToastInStore({
          severity: response.severity,
          summary: 'Erreur de connexion',
          detail: "L'identifiant ou le mot de passe est incorrect.",
        });
        resetToastInStore();
      }
    } catch (err) {
      setErrorInState(err.response ? err.response.status : err.status || 500);
    }
    setIsLoading(false);
  };

  const handleChange = (e) => {
    setFormValues({
      ...formValues,
      [e.target.name]: e.target.value,
    });
  };

  return (
    <main>
      <section>
        <form onSubmit={(e) => handleSubmit(e)} className="login-controller">
          <div>
            <label htmlFor="username-field" className="username-label">Nom d'utilisateur</label>
            <InputText
              className="username-field"
              value={formValues.username}
              id="username-field"
              name="username"
              aria-describedby="username-field-help"
              onChange={(e) => handleChange(e)}
            />
          </div>
          <div className="password-field-container">
            <label htmlFor="password-field" className="password-label">Mot de passe</label>
            <InputText
              className="password-field"
              value={formValues.password}
              id="password-field"
              name="password"
              type="password"
              aria-describedby="password-field-help"
              onChange={(e) => handleChange(e)}
            />
          </div>
          <Button type="submit" label={!isLoading ? 'Se conneceter' : null}>
            {
              isLoading ? <ProgressSpinner style={{ width: '22px', height: '23px' }} strokeWidth="5" animationDuration="1s" /> : null
            }
          </Button>
        </form>
      </section>
    </main>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setUserInStore: (dataToStore) => dispatch(setUser(dataToStore)),
  setToastInStore: (toastConfig) => dispatch(setToast(toastConfig)),
  resetToastInStore: () => dispatch(resetToast()),
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default withRouter(connect(null, mapDispatchToProps)(LoginPage));
