import Alert from '../entities/alert';
import AbstractService from './abstractService';

export default class AlertService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/alerts'));
  }

  get(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((response) => {
          const alerts = [];
          response.forEach((alert) => {
            alerts.push(Alert.parse(alert));
          });
          resolve(alerts);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }
}
