// import Airport from './airport';
import Translation from './translation';

/**
 * Class that represents a bulletin.
 * @since 0.0.1
 * @author JACQUEL Romain
 * @author METIVIER Nicolas
 */
class Bulletin {
  constructor() {
    this.identifier = null;
    this.name = null;
    this.airport = null;
    this.date = null;
    this.report_type = null;
    this.raw_text = null;
    this.translations = [];
    this.status = null;
    this.isOutdated = null;
    this.error = null;
    this.isRowOpen = false;
    this.airport_icao = null;
    this.airport_name = null;
    this.decoded_text = null;
    this.validityLeft = null;
    this.validityLeftColor = null;
    this.vocal_synthesis_date = null;
    this.className = null;
    this.errorMessage = null;
    this.weatherTelemetries = [];
  }

  static compare(previous, next) {
    if (previous.language < next.language) {
      return -1;
    }
    if (previous.language > next.language) {
      return 1;
    }
    return 0;
  }

  /**
   * Function to convert a JSON to Bulletin object.
   * @param {JSON} json Bulletin as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.name !== undefined) {
          // if (json.airport !== undefined) {
          if (json.date !== undefined) {
            if (json.reportType !== undefined) {
              if (json.rawText !== undefined) {
                if (json.status !== undefined) {
                  if (json.translations !== undefined) {
                    const bulletin = new Bulletin();
                    bulletin.identifier = json.identifier;
                    bulletin.name = json.name;
                    // bulletin.airport = Airport.parse(json.airport);
                    bulletin.date = json.date;
                    bulletin.report_type = json.reportType;
                    bulletin.raw_text = json.rawText;
                    bulletin.status = json.status;
                    bulletin.isOutdated = json.isOutdated;
                    bulletin.error = json.error;
                    if (json.translations != null && Array.isArray(json.translations)) {
                      for (let index = 0; index < json.translations.length; index += 1) {
                        json.translations[index].bulletinIdentifier = json.identifier;
                        json.translations[index].bulletinName = json.name;
                        bulletin.translations.push(Translation.parse(json.translations[index]));
                        if (json.translations[index].status === 'ERROR') {
                          bulletin.status = json.translations[index].status;
                        }
                      }
                    }
                    bulletin.weatherTelemetries = json.weatherTelemetries;
                    bulletin.translations.sort(Bulletin.compare);
                    return bulletin;
                  }
                }
              }
            }
          }
          // }
        }
      }
    }
    return null;
  }
}

export default Bulletin;
