import NetworkCard from './NetworkCard';

export default class Hardware {
  constructor() {
    this.identifier = null;
    this.hostname = null;
    this.name = null;
    this.status = null;
    this.networkInterfaces = [];
    this.description = null;
  }

  static parse(json) {
    if (json) {
      // if (json.identifier !== undefined) {
      if (json.hostname !== undefined) {
        if (json.name !== undefined) {
          if (json.status !== undefined) {
            const hardware = new Hardware();
            // hardware.identifier = json.identifier;
            hardware.hostname = json.hostname;
            hardware.name = json.name;
            hardware.status = json.status;
            if (json.networkInterfaces != null && Array.isArray(json.networkInterfaces)) {
              for (let index = 0; index < json.networkInterfaces.length; index += 1) {
                hardware.networkInterfaces.push(NetworkCard.parse(json.networkInterfaces[index]));
              }
            }
            return hardware;
          }
        }
      }
    }
    return null;
  }
}
