import Airport from '../entities/airport';
import Hardware from '../entities/Hardware';
import RtcGateway from '../entities/RtcGateway';
import Software from '../entities/Software';
import StapServer from '../entities/StapServer';
import AbstractService from './abstractService';

export default class MonitoringService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/monitoring'));
    this.baseUrl = String(process.env.REACT_APP_BASE_URL_WS).concat('/monitoring');
  }

  getSoftwares(filters) {
    this.url = String(this.baseUrl).concat('/softwares');
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const softwares = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              softwares.push(Software.parse(json[index]));
            }
          } else {
            softwares.push(Software.parse(json));
          }

          for (let index = 0; index < softwares.length; index += 1) {
            switch (softwares[index].name) {
              case 'ACAPELA':
                softwares[index].description = process.env.REACT_APP_SOFTWARE_ACAPELA;
                break;
              case 'ASTERISK':
                softwares[index].description = process.env.REACT_APP_SOFTWARE_ASTERISK;
                break;
              case 'LDAP':
                softwares[index].description = process.env.REACT_APP_SOFTWARE_LDAP;
                break;
              case 'SQL':
                softwares[index].description = process.env.REACT_APP_SOFTWARE_SQL;
                break;
              case 'DOCKER':
                softwares[index].description = process.env.REACT_APP_SOFTWARE_DOCKER;
                break;
              default:
                break;
            }
          }
          resolve(softwares.sort(MonitoringService.sortByName));
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  static sortByName(previous, next) {
    if (previous.description > next.description) {
      return 1;
    }
    return -1;
  }

  getHardwares(filters) {
    this.url = String(this.baseUrl).concat('/hardwares');
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const hardwares = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              hardwares.push(Hardware.parse(json[index]));
            }
          } else {
            hardwares.push(Hardware.parse(json));
          }
          for (let index = 0; index < hardwares.length; index += 1) {
            switch (hardwares[index].name) {
              case 'AUDIO_CODE':
                hardwares[index].description = process.env.REACT_APP_HARDWARE_AUDIO_CODE;
                break;
              case 'BOITIER_ES':
                hardwares[index].description = process.env.REACT_APP_HARDWARE_BOITIER_ES;
                break;
              case 'SERVER':
                hardwares[index].description = process.env.REACT_APP_HARDWARE_SERVER;
                break;
              case 'ROUTER_4G':
                hardwares[index].description = process.env.REACT_APP_HARDWARE_ROUTER_4G;
                break;
              default:
                break;
            }
          }
          resolve(hardwares.sort(MonitoringService.sortByName));
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  getPerformences(filters) {
    this.url = String(this.baseUrl).concat('/performences');
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          let servers = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              servers.push(StapServer.parse(json[index]));
            }
          } else {
            servers = StapServer.parse(json);
          }
          resolve(servers);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  getRtcGateways(filters) {
    this.url = String(this.baseUrl).concat('/rtc-gateways');
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const airports = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              airports.push(RtcGateway.parse(json[index]));
            }
          } else {
            airports.push(RtcGateway.parse(json));
          }
          resolve(airports);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  getVhfs(filters) {
    this.url = String(this.baseUrl).concat('/vhfs');
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const airports = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              airports.push(Airport.parse(json[index]));
            }
          } else {
            airports.push(Airport.parse(json));
          }
          resolve(airports);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  getTwilightSensor(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const airports = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              airports.push(Airport.parse(json[index]));
            }
          } else {
            airports.push(Airport.parse(json));
          }
          resolve(airports);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }
}
