/**
 * Class that represents an airport.
 * @since 0.9.8
 * @author METIVIER Nicolas
 */
export default class Version {
  constructor() {
    this.version = 0;
    this.release = 0;
    this.bugfix = 0;
    this.tag = null;
    this.full = null;
  }

  /**
   * Function to convert a JSON to Alert object.
   * @param {JSON} json Alert as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.version !== undefined) {
        if (json.release !== undefined) {
          if (json.bugfix !== undefined) {
            if (json.tag !== undefined) {
              if (json.full !== undefined) {
                const version = new Version();
                version.version = json.version;
                version.release = json.release;
                version.bugfix = json.bugfix;
                version.tag = json.tag;
                version.full = json.full;
                return version;
              }
            }
          }
        }
      }
    }
    return null;
  }
}
