import axios from 'axios';
import AbstractCrudService from './abstractService';
import AdditionalMessage from '../entities/additionalMessage';

export default class AdditionalMessageService extends AbstractCrudService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/additionals-messages'));
  }

  get() {
    return new Promise((resolve, reject) => {
      super.get()
        .then((result) => {
          const ADDITIONALS_MESSAGES = [];
          result.forEach((additionalMessage) => {
            ADDITIONALS_MESSAGES.push(AdditionalMessage.parse(additionalMessage));
          });
          resolve(ADDITIONALS_MESSAGES);
        }).catch((e) => {
          reject(e);
        });
    });
  }

  find(identifier) {
    return new Promise((resolve, reject) => {
      super.find({}, identifier)
        .then((result) => {
          resolve(result);
        })
        .catch((e) => {
          reject(e);
        });
    });
  }

  create(formData, jwt) {
    return new Promise((resolve, reject) => {
      const headers = {
        Authorization: `Bearer ${jwt}`,
        'Content-Type': 'multipart/form-data',
      };
      console.log(headers);
      axios.post(`${this.url}/upload`, formData, headers)
        .then((result) => {
          resolve(result.data);
        })
        .catch((e) => {
          reject(e);
        });
    });
  }

  update(additionalMessage) {
    return new Promise((resolve, reject) => {
      super.update({}, additionalMessage.identifier, additionalMessage)
        .then((result) => {
          console.log(result);
          resolve(result);
        })
        .catch((e) => {
          reject(e);
        });
    });
  }

  download(token, identifier) {
    return new Promise((resolve, reject) => {
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      axios.get(`${this.url}/${identifier}/download`, { responseType: 'blob', headers })
        .then((response) => {
          console.log(response);
          resolve({ status: response.status, blob: response.data });
        })
        .catch((e) => {
          reject(e);
        });
    });
  }
}
