/**
 * Class that represents an airport.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
class Alert {
  static total = 0;

  constructor() {
    Alert.total += 1;
    this.identifier = Alert.total;
    this.date = new Date();
    this.type = '';
    this.detail = '';
    this.notified = false;
  }

  /**
   * Function to convert a JSON to Alert object.
   * @param {JSON} json Alert as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.type !== undefined) {
          if (json.detail !== undefined) {
            if (json.date !== undefined) {
              if (json.notified !== undefined) {
                const alert = new Alert();
                alert.identifier = json.identifier;
                alert.type = json.type;
                alert.detail = json.detail;
                alert.notified = json.notified;
                alert.date = new Date(json.date);
                return alert;
              }
            }
          }
        }
      }
    }
    return null;
  }
}

export default Alert;
