import AerialVhf from '../entities/aerialVhf';
import AbstractCrudService from './abstractService';

export default class AerialVhfService extends AbstractCrudService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/aerial-vhf'));
  }

  get(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const aerialsVHF = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              aerialsVHF.push(AerialVhf.parse(json[index]));
            }
          } else {
            aerialsVHF.push(AerialVhf.parse(json));
          }
          resolve(aerialsVHF);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }
}
