/* eslint-disable max-len */
import React, {
  useEffect, useState, useCallback, useRef,
} from 'react';
import { Toast } from 'primereact/toast';
import { Calendar } from 'primereact/calendar';
import { Skeleton } from 'primereact/skeleton';
import { Card } from 'primereact/card';
import BulletinService from '../../../services/bulletinService';
import './styles.scss';
import StatisticsService from '../../../services/statisticsService';
import AdvancedBulltinTable from '../../components/AdvancedBulltinTable';

const HistoryPage = () => {
  const [bulletins, setBulletins] = useState([]);
  const [totalRtcCalls, setTotalRtcCalls] = useState(0);
  const [totalVhfCalls, setTotalVhfCalls] = useState(0);
  const toast = useRef(null);

  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [uptime, setUptime] = useState(0);

  const updateTotalRtcCallsCallback = useCallback(() => {
    let maxRtcBroadcast = 0;
    if (bulletins) {
      for (let bulletinIndex = 0; bulletinIndex < bulletins.length; bulletinIndex += 1) {
        const bulletin = bulletins[bulletinIndex];
        if (bulletin) {
          for (let translationIndex = 0; translationIndex < bulletin.translations.length; translationIndex += 1) {
            const translation = bulletin.translations[translationIndex];

            if (translation) {
              let totalRtcBroadcastForTranslation = 0;
              for (let broadcastIndex = 0; broadcastIndex < translation.broadcasts.length; broadcastIndex += 1) {
                const broadcast = translation.broadcasts[broadcastIndex];
                if (broadcast) {
                  if (broadcast.broadcastType === 'RTC') {
                    totalRtcBroadcastForTranslation += 1;
                  }
                }
              }
              maxRtcBroadcast += totalRtcBroadcastForTranslation;
            }
          }
        }
      }
    }
    setTotalRtcCalls(maxRtcBroadcast);
  }, [bulletins, setTotalRtcCalls]);

  const updateTotalVhfCallsCallback = useCallback(() => {
    let maxVhfBroadcast = 0;
    if (bulletins) {
      for (let bulletinIndex = 0; bulletinIndex < bulletins.length; bulletinIndex += 1) {
        const bulletin = bulletins[bulletinIndex];
        if (bulletin) {
          for (let translationIndex = 0; translationIndex < bulletin.translations.length; translationIndex += 1) {
            const translation = bulletin.translations[translationIndex];

            if (translation) {
              let totalVhfBroadcastForTranslation = 0;
              for (let broadcastIndex = 0; broadcastIndex < translation.broadcasts.length; broadcastIndex += 1) {
                const broadcast = translation.broadcasts[broadcastIndex];
                if (broadcast) {
                  if (broadcast.broadcastType === 'VHF') {
                    totalVhfBroadcastForTranslation += 1;
                  }
                }
              }
              maxVhfBroadcast += totalVhfBroadcastForTranslation;
            }
          }
        }
      }
    }
    setTotalVhfCalls(maxVhfBroadcast);
  }, [bulletins, setTotalVhfCalls]);

  const bulletinTableHeader = () => (
    <div className="table-header">
      <h3>Liste des bulletins historisés</h3>
    </div>
  );

  useEffect(() => {
    let maxRtcBroadcast = 0;
    let maxVhfBroadcast = 0;
    if (bulletins) {
      for (let bulletinIndex = 0; bulletinIndex < bulletins.length; bulletinIndex += 1) {
        const bulletin = bulletins[bulletinIndex];
        if (bulletin) {
          for (let translationIndex = 0; translationIndex < bulletin.translations.length; translationIndex += 1) {
            const translation = bulletin.translations[translationIndex];

            if (translation) {
              let totalRtcBroadcastForTranslation = 0;
              let totalVhfBroadcastForTranslation = 0;
              for (let broadcastIndex = 0; broadcastIndex < translation.broadcasts.length; broadcastIndex += 1) {
                const broadcast = translation.broadcasts[broadcastIndex];
                if (broadcast) {
                  if (broadcast.broadcastType === 'RTC') {
                    totalRtcBroadcastForTranslation += 1;
                  } else if (broadcast.broadcastType === 'VHF') {
                    totalVhfBroadcastForTranslation += 1;
                  }
                }
              }
              maxVhfBroadcast += totalVhfBroadcastForTranslation;
              maxRtcBroadcast += totalRtcBroadcastForTranslation;
            }
          }
        }
      }
    }
    setTotalRtcCalls(maxRtcBroadcast);
    setTotalVhfCalls(maxVhfBroadcast);
  }, [bulletins, setTotalRtcCalls, setTotalVhfCalls]);

  useEffect(() => {
    const bulletinsService = new BulletinService();
    if (startDate && endDate) {
      if (Date.parse(startDate) > Date.parse(endDate)) {
        toast.current.show({
          severity: 'warn', summary: "Message d'avertissement", detail: 'La date de début doit être inférieure à la date de fin', life: 4000,
        });
      } else {
        bulletinsService.get({ broadcasted_from: Date.parse(startDate), broadcasted_to: Date.parse(endDate), isTelemetry: true })
          .then((list) => {
            setBulletins(list);
          })
          .catch((error) => console.error(error));
      }
    } else if (startDate) {
      bulletinsService.get({ broadcasted_from: Date.parse(startDate), broadcasted_to: (new Date()).getTime(), isTelemetry: true })
        .then((list) => {
          setBulletins(list);
        })
        .catch((error) => console.error(error));
    } else {
      bulletinsService.get({ isTelemetry: true, broadcasted_to: (new Date()).getTime() })
        .then((list) => {
          setBulletins(list);
        })
        .catch((error) => console.error(error));
    }
  }, [setBulletins, startDate, endDate]);

  const updateUptime = useCallback(() => {
    const SERVICE = new StatisticsService();
    SERVICE.get({}).then((json) => {
      setUptime(json.metrics.APPLICAITON_UPTIME);
    }).catch(() => {
      setUptime(0);
    });
  }, [setUptime]);

  useEffect(() => {
    updateUptime();
    updateTotalRtcCallsCallback();
    updateTotalVhfCallsCallback();
  }, [updateUptime, updateTotalRtcCallsCallback, updateTotalVhfCallsCallback]);

  const getUptime = () => {
    try {
      // 415638
      // 733747 = 12 mins
      // const durationInSeconds = uptime / 1000;
      // const seconds = Math.floor(durationInSeconds % 60);
      // const minutes = Math.floor((durationInSeconds / 60) % 60);
      // const hours = Math.floor(((durationInSeconds / 60) / 60) % 24);
      let seconds = Math.floor((uptime / 1000) % 60);
      let minutes = Math.floor((uptime / (1000 * 60)) % 60);
      let hours = Math.floor((uptime / (1000 * 60 * 60)) % 24);
      const days = Math.floor((uptime / (1000 * 60 * 60 * 24)) % 24);
      hours = (hours < 10) ? String('0').concat(hours) : hours;
      minutes = (minutes < 10) ? String('0').concat(minutes) : minutes;
      seconds = (seconds < 10) ? String('0').concat(seconds) : seconds;

      return 'DDj HHh MMm SSs'.replace('DD', days).replace('HH', hours).replace('MM', minutes).replace('SS', seconds);
    } catch (error) {
      return (<Skeleton />);
    }
  };

  return (
    <main className="page page-bulletins page-container">
      <Toast ref={toast} baseZIndex={10000} />
      <section>
        <Card className="StatisticPanel" title={getUptime} subTitle="Temps d'éxécution du système ride4ever." />
        <div className="date-picker-container">
          <div className="calendar-container">
            <div className="start-date-container">
              <h5>Date de début</h5>
              <Calendar
                showTime
                value={startDate}
                dateFormat="dd/mm/yy"
                placeholder="Selectionnez une date"
                onChange={(e) => setStartDate(e.value)}
              />
            </div>
            <div className="end-date-container">
              <h5>Date de fin</h5>
              <Calendar
                showTime
                value={endDate}
                dateFormat="dd/mm/yy"
                placeholder="Selectionnez une date"
                onChange={(e) => setEndDate(e.value)}
              />
            </div>
          </div>
        </div>
        <div className="StatisticsContainer">
          <Card className="StatisticPanel" title={totalRtcCalls} subTitle="Nombre d'appels sur le serveur vocal." />
          <Card className="StatisticPanel" title={totalVhfCalls} subTitle="Nombre d'appels VHF." />
          {/* <Card className="StatisticPanel" title={totalLightingActivation} subTitle="Nombre de déclencehements du balisage." /> */}
        </div>
        <div className="bulletin-table-container">
          <AdvancedBulltinTable
            header={bulletinTableHeader}
            values={bulletins}
            refreshData={false}
          />
        </div>
      </section>
    </main>
  );
};

export default HistoryPage;
