export default class AerialVhf {
  constructor() {
    this.identifier = 0;
    this.isTransmitting = false;
    this.isReceiving = false;
  }

  static parse(json) {
    if (json) {
      const aerialVhf = new AerialVhf();
      aerialVhf.identifier = json.identifier;
      aerialVhf.isTransmitting = json.isTransmitting;
      aerialVhf.isReceiving = json.isReceiving;
      return aerialVhf;
    }
    return null;
  }
}
