/* eslint-disable react/prop-types */
import React, { useEffect, useState } from 'react';
// import PropTypes from 'prop-types';
import { ProgressBar } from 'primereact/progressbar';
import './styles.scss';
import setProgressBarColor from '../../../tools/set-progress-bar-color';

const HardwareMonitoringPanel = ({ processor, memory, storage }) => {
  const [cpu, setCpu] = useState(processor);
  const [ram, setRam] = useState(memory);
  const [hardDrives, setHardDrives] = useState(storage);

  useEffect(() => {
    setCpu(processor);
  }, [setCpu, processor]);

  useEffect(() => {
    setRam(memory);
  }, [setRam, memory]);

  useEffect(() => {
    setHardDrives(storage);
  }, [setHardDrives, storage]);

  const renderCpu = () => {
    if (cpu) {
      return (
        <div className="data-container cpu">
          <span>Processeur</span>
          <ProgressBar
            color={setProgressBarColor(cpu.usagePercent)}
            value={cpu.usagePercent}
          />
        </div>
      );
    }
    return null;
  };
  const renderRam = () => {
    if (ram) {
      return (
        <div className="data-container ram">
          <span>Mémoire RAM</span>
          <ProgressBar
            color={setProgressBarColor(ram.usagePercent)}
            value={ram.usagePercent}
          />
        </div>
      );
    }
    return null;
  };
  const renderStorage = () => {
    if (hardDrives && hardDrives.length > 0) {
      return hardDrives.map((storageDisk) => (
        <div key={storageDisk} className="disks-container data-container">
          <span>
            Disques de stockage
            {' '}
            {storageDisk.name}
          </span>
          <ProgressBar
            color={setProgressBarColor(storageDisk.usagePercent)}
            value={storageDisk.usagePercent}
          />
        </div>
      ));
    }
    return null;
  };

  return (
    <div className="fourth-part">
      {renderCpu()}
      {renderRam()}
      {renderStorage()}
    </div>
  );
};

export default HardwareMonitoringPanel;
