/* eslint-disable react/prop-types */
import React, {
  useState, useEffect, useCallback, useRef,
} from 'react';
import { connect } from 'react-redux';
import { Toast } from 'primereact/toast';
import { setError } from '../../../store/error/errorAction';
import BulletinService from '../../../services/bulletinService';
import BulletinTable from '../../components/BulletinTable';

const BulletinPage = ({ setErrorInState }) => {
  const [bulletins, setBulletins] = useState([]);
  const [doRequest, setDoRequest] = useState(false);
  const toast = useRef(null);

  // eslint-disable-next-line consistent-return
  const getBulletins = useCallback(async () => {
    try {
      const bulletinsService = new BulletinService();
      const bulletinsList = await bulletinsService.get({ limit: 60, isTelemetry: true });

      setBulletins(bulletinsList);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  }, [setErrorInState]);

  const getBulletinsAfterSocketsUpdate = useCallback(() => {
    if (doRequest) {
      getBulletins();
      setDoRequest(false);
    }
  }, [doRequest, getBulletins]);

  useEffect(() => {
    getBulletinsAfterSocketsUpdate();
  }, [getBulletinsAfterSocketsUpdate]);

  useEffect(() => {
    getBulletins();
  }, [getBulletins]);

  const bulletinTableHeader = () => (
    <div className="table-header">
      <h3>Liste des bulletins</h3>
    </div>
  );

  return (
    <main className="page page-bulletins page-container">
      <Toast ref={toast} baseZIndex={10000} />
      <section>
        <BulletinTable
          header={bulletinTableHeader}
          values={bulletins}
          setDoRequest={setDoRequest}
          refreshData
        />
      </section>
    </main>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(BulletinPage);
