/**
 * Class that represents an event log.
 * @since 0.10.2
 * @author METIVIER Nicolas
 */
class EventLog {
  constructor() {
    this.month = null;
    this.names = null;
  }

  static compare(previous, next) {
    if (previous.month < next.month) {
      return -1;
    }
    if (previous.month > next.month) {
      return 1;
    }
    return 0;
  }

  /**
   * Function to convert a JSON to Bulletin object.
   * @param {JSON} json Bulletin as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.month !== undefined) {
        if (json.names !== undefined) {
          const eventLogBackup = new EventLog();
          eventLogBackup.month = json.month;
          eventLogBackup.names = json.names;
          return eventLogBackup;
        }
      }
    }
    return null;
  }
}

export default EventLog;
