export default class Broadcast {
  constructor() {
    this.identifier = null;
    this.date = null;
    this.broadcastType = null;
    this.broadcastOrder = null;
    this.isBroadcasting = false;
  }

  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.date !== undefined) {
          if (json.broadcastType !== undefined) {
            const broadcast = new Broadcast();
            broadcast.identifier = json.identifier;
            broadcast.date = json.date;
            broadcast.broadcastType = json.broadcastType;
            broadcast.broadcastOrder = json.broadcastOrder;
            broadcast.isBroadcasting = json.isBroadcasting;
            return broadcast;
          }
        }
      }
    }
    return null;
  }
}
