/**
 * Class that represents a localization.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
class Localization {
  constructor() {
    this.identifier = 0;
    this.city = '';
    this.country = '';
  }

  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.city !== undefined) {
          if (json.country !== undefined) {
            const localization = new Localization();
            localization.identifier = json.identifier;
            localization.city = json.city;
            localization.country = json.country;
            return localization;
          }
        }
      }
    }
    return null;
  }
}

export default Localization;
