/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import React, {
  useCallback, useEffect, useState, useMemo,
} from 'react';
import { Fieldset } from 'primereact/fieldset';
import { connect } from 'react-redux';
import MessageCreation from '../../components/MessageCreation';
import TableAdditionalMessage from '../../components/TableAdditionalMessage';
import { setError } from '../../../store/error/errorAction';
import AdditionalMessageService from '../../../services/additionalMessageService';
import './styles.scss';

const BroadcastMessagePage = ({ setErrorInState }) => {
  const [additionalsMessages, setAdditionalsMessages] = useState(null);
  const audio = document.getElementsByClassName('audio-element')[0];
  const [audioURL, setAudioURL] = useState(null);

  const ADDITIONAL_MESSAGE_SERVICE = useMemo(() => new AdditionalMessageService(), []);

  const getAdditionalsMessages = useCallback(() => {
    ADDITIONAL_MESSAGE_SERVICE.get()
      .then((response) => {
        console.log(response);
        setAdditionalsMessages(response);
      })
      .catch((e) => {
        setErrorInState(e.response ? e.response.status : e.status || 500);
      });
  }, [ADDITIONAL_MESSAGE_SERVICE, setErrorInState]);

  useEffect(() => {
    getAdditionalsMessages();
  }, [getAdditionalsMessages]);

  console.log(additionalsMessages);

  return (
    <main>
      <section className="broadcast-message-container">
        <audio
          className="audio-element"
          src={audioURL}
          type="audio/webm;codecs=opus"
        />
        <Fieldset legend="Création de message" toggleable>
          <MessageCreation
            setAdditionalsMessages={setAdditionalsMessages}
          />
        </Fieldset>
        <Fieldset legend="Messages complémentaires" toggleable>
          <TableAdditionalMessage
            setAdditionalsMessages={setAdditionalsMessages}
            additionalsMessages={additionalsMessages}
            audio={audio}
            audioURL={audioURL}
            setAudioURL={setAudioURL}
          />
        </Fieldset>
      </section>
    </main>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(BroadcastMessagePage);
