import AbstractService from './abstractService';

export default class StatisticsService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/statistics'));
  }

  get(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          resolve(json);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }
}
