import axios from 'axios';
import Bulletin from '../entities/bulletin';
import AbstractService from './abstractService';

export default class BulletinService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/bulletins'));
  }

  get(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const bulletins = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              bulletins.push(Bulletin.parse(json[index]));
            }
          } else {
            bulletins.push(Bulletin.parse(json));
          }
          resolve(bulletins);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }

  async getAudioBulletin(token, language, idBulletin) {
    try {
      const headers = {
        Authorization: `Bearer ${token}`,
      };
      const response = await axios.get(`${this.url}/${idBulletin}/${language.toUpperCase()}/download`, { responseType: 'blob', headers });
      return { status: response.status, blob: response.data };
    } catch (error) {
      console.error(error);
      if (error.response) {
        return { status: error.response.status, message: error.response.statusText };
      }
      return { status: 500, message: error.message };
    }
  }
}
