import EventLogBackup from '../entities/EventLogBackup';
import AbstractService from './abstractService';

export default class EventLogBackupService extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/event-log/backups'));
  }

  get(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const eventLogs = [];
          if (Array.isArray(json)) {
            for (let index = 0; index < json.length; index += 1) {
              eventLogs.push(EventLogBackup.parse(json[index]));
            }
          } else {
            eventLogs.push(EventLogBackup.parse(json));
          }
          resolve(eventLogs);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }
}
