import * as errorAction from './errorActionType';

const initialState = {
  requestStatus: null,
};

export default function errorReducer(state = initialState, action) {
  switch (action.type) {
    case errorAction.SET_ERROR:
      return {
        ...state,
        requestStatus: action.data,
      };
    case errorAction.RESET_ERROR:
      return {
        ...state,
        ...initialState,
      };
    default:
      return state;
  }
}
