/* eslint-disable react/prop-types */
/* eslint-disable max-len */
/* eslint-disable class-methods-use-this */
import React, {
  useState, useCallback, useEffect,
} from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { Fieldset } from 'primereact/fieldset';
import './styles.scss';
import WebSocketClientEventListener from '../../../tools/websocket/websocket-eventlistener';
import WebsocketService from '../../../services/websocketService';
import StapSystem from '../../../entities/StapSystem';
import Vhf from '../../../entities/vhf';
import TableSettingsNetwork from '../../components/TableSettingsNetwork';
import VhfCard from '../../components/VhfCard';
import { createAlert } from '../../../store/alert/alertAction';
import { setError } from '../../../store/error/errorAction';
import Alert from '../../../entities/alert';
import AnomalyType from '../../../enumerations/AnomalyType';
import RtcGatewayCard from '../../components/RtcGatewayCard';
import MonitoringService from '../../../services/monitoringService';
import AerialVhfService from '../../../services/aerialVhfService';
import CardListBox from '../../components/CardListBox';
import HardwareMonitoringPanel from '../../components/HardwareMonitoringPanel';
import WatchDogMethodsProvider from '../../../services/watchDogService';

const MonitoringPage = ({ createAlertInStore, setErrorInState }) => {
  class MonitoringEventListener extends WebSocketClientEventListener {
    onError() {
    }

    onMessage(event) {
      const json = WebSocketClientEventListener.getJson(event.data);
      if (json) {
        const stapSystem = StapSystem.parse(json);
        if (stapSystem !== null) {
          setInformations(stapSystem);
        } else {
          const vhf = Vhf.parse(json);
          if (vhf !== null) {
            setVhf(vhf);
          }
        }
      }
    }

    onOpen() {
    }

    onClose() {}
  }

  const [informations, setInformations] = useState(null);
  const [vhf, setVhf] = useState(new Vhf());
  const [rtcGateways, setRtcGateways] = useState(null);
  const [hardwares, setHardwares] = useState(null);
  const [softwares, setSoftwares] = useState(null);
  const [performence, setPerformence] = useState(null);
  const [watchDogs, setWatchDogs] = useState(null);

  WebsocketService.getInstance().setEventListener(new MonitoringEventListener());

  const getSystemInformations = useCallback(async () => {
    const monitoringService = new MonitoringService();
    const watchDogService = new WatchDogMethodsProvider();
    const aerialVhfService = new AerialVhfService();

    monitoringService.getRtcGateways({}).then((response) => {
      setRtcGateways(response);
    }).catch((e) => {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    });

    monitoringService.getHardwares({}).then((response) => {
      setHardwares(response);
    }).catch((e) => {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    });

    monitoringService.getSoftwares({}).then((response) => {
      setSoftwares(response);
    }).catch((e) => {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    });

    monitoringService.getPerformences({}).then((response) => {
      setPerformence(response);
    }).catch((e) => {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    });

    aerialVhfService.get({}).then((response) => {
      if (response && Array.isArray(response) && response.length > 0) {
        setVhf(response[0]);
      } else {
        setVhf(new Vhf());
      }
    }).catch((e) => {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    });

    watchDogService.getAllWatchDogs().then((response) => {
      setWatchDogs(response);
    }).catch((e) => {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    });
  }, [setErrorInState]);

  useEffect(() => {
    getSystemInformations();
  }, [getSystemInformations]);

  const updateStatpSystem = useCallback(() => {
    if (informations) {
      for (let index = 0; index < informations.hardwares.length; index += 1) {
        const hardware = informations.hardwares[index];
        if (hardware.status === 'Critic' || hardware.status === 'Déconnecté') {
          const alert = new Alert();
          alert.date = new Date();
          alert.detail = String(hardware.description).concat(' subit une erreur.');
          alert.type = AnomalyType.SOFTWARE;
          createAlertInStore(alert);
        }
      }
      for (let index = 0; index < informations.softwares.length; index += 1) {
        const sofware = informations.softwares[index];
        if (sofware.status === 'Critic' || sofware.status === 'Déconnecté') {
          const alert = new Alert();
          alert.date = new Date();
          alert.detail = String(sofware.description).concat(' subit une erreur.');
          alert.type = AnomalyType.SOFTWARE;
          createAlertInStore(alert);
        }
      }
    }
  }, [createAlertInStore, informations]);

  useEffect(() => {
    updateStatpSystem();
  }, [updateStatpSystem]);
  return (
    <main>
      <section>
        <Fieldset className="monitoring-fieldset" legend="Surveillance en direct" toggleable>
          <div className="first-part">
            <VhfCard vhf={vhf} />
            <RtcGatewayCard rtcGateways={rtcGateways} />
          </div>
          <div className="second-part">
            <CardListBox title="Logiciels" data={softwares} field="name" />
            <CardListBox title="Matériels" data={hardwares} field="name" />
          </div>
          <div className="third-part">
            <CardListBox title="Processus" data={watchDogs} field="name" watchDog />
          </div>
          {
            (performence) ? <HardwareMonitoringPanel processor={performence.cpu} memory={performence.memory} storage={performence.storageDisks} /> : null
          }
        </Fieldset>
        <Fieldset className="setting-newtwork-fieldset" legend="Paramètres des appareils connectés" toggleable>
          <TableSettingsNetwork hards={hardwares} />
        </Fieldset>
      </section>
    </main>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  createAlertInStore: (dataToStore) => dispatch(createAlert(dataToStore)),
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),

});

export default withRouter(connect(null, mapDispatchToProps)(MonitoringPage));
