/* eslint-disable consistent-return */
/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
/* eslint-disable max-len */
import React, {
  useState, useEffect, useRef, useCallback,
} from 'react';
import { Toast } from 'primereact/toast';
import { Card } from 'primereact/card';
import { MultiSelect } from 'primereact/multiselect';
import { connect } from 'react-redux';
import { Button } from 'primereact/button';
import { setError } from '../../../store/error/errorAction';
import BroadcastConfigurationService from '../../../services/BroadcastConfigurationService';
import './styles.scss';

const BroadcastConfigurationPage = ({ setErrorInState }) => {
  const [broadcastsConfigurations, setBroadcastsConfigurations] = useState([]);
  const [initialConfigurations, setInitialConfigurations] = useState([]);
  const toast = useRef(null);
  const multiSelectOptions = ['FRENCH', 'ENGLISH'];

  const getConfigurationsService = useCallback(async () => {
    try {
      const BROADCAST_CONFIGURATION_SERVICE = new BroadcastConfigurationService();
      const broadcastConfigurationList = await BROADCAST_CONFIGURATION_SERVICE.get({});

      setBroadcastsConfigurations([...broadcastConfigurationList]);
      broadcastConfigurationList.forEach((configuration) => {
        setInitialConfigurations((config) => [...config, { type: configuration.type, languages: [...configuration.languages] }]);
      });
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [setErrorInState]);

  useEffect(() => {
    getConfigurationsService();
  }, [getConfigurationsService]);

  const updateLanguage = (e) => {
    const { value } = e.target;
    if (value.length === 0) {
      toast.current.show({
        severity: 'warn', summary: "Message d'avertissement", detail: 'Au moins une langue doit être sélectionnée',
      });
    } else {
      const newArray = [...broadcastsConfigurations];
      const elementIndex = broadcastsConfigurations.findIndex((element) => element.name === e.target.name);
      newArray[elementIndex].languages = value;
      setBroadcastsConfigurations(newArray);
    }
  };

  const updateBroadcastConfiguration = (broadcastConfiguration) => {
    try {
      if (broadcastConfiguration.languages.length === 0) {
        toast.current.show({
          severity: 'warn', summary: "Message d'avertissement", detail: 'Au moins une langue doit être sélectionnée',
        });
      } else {
        const BROADCAST_CONFIGURATION_SERVICE = new BroadcastConfigurationService();
        BROADCAST_CONFIGURATION_SERVICE.create({}, broadcastConfiguration);

        const initialConfigurationsToUpdate = [...initialConfigurations];
        initialConfigurationsToUpdate.forEach((config) => {
          if (config.type === broadcastConfiguration.type) {
            config.languages = [...broadcastConfiguration.languages];
          }
        });
        setInitialConfigurations([...initialConfigurationsToUpdate]);
        toast.current.show({
          severity: 'success', summary: 'Message de succès', detail: 'La configuration à bien été modfiée',
        });
      }
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  };

  const setButtonToDisable = (type) => {
    let isDisabled = true;
    if (broadcastsConfigurations.length > 0 && broadcastsConfigurations.length === initialConfigurations.length) {
      const currentValue = broadcastsConfigurations.find((config) => config.type === type);
      const initialValue = initialConfigurations.find((config) => config.type === type);

      if (currentValue.languages.length !== initialValue.languages.length) {
        isDisabled = false;
      }
      if (currentValue.languages.length === initialValue.languages.length) {
        currentValue.languages.forEach((language) => {
          if (!initialValue.languages.includes(language)) {
            isDisabled = false;
          }
        });
      }
    }
    return isDisabled;
  };

  console.log(setButtonToDisable('RTC'));

  const renderCards = () => {
    if (broadcastsConfigurations.length > 0) {
      return broadcastsConfigurations.map((broadcastConfiguration) => (
        <Card key={broadcastConfiguration} title={`Configuration audio ${broadcastConfiguration.type} ${broadcastConfiguration.name}`} style={{ width: '22rem', marginBottom: '2em' }}>
          <MultiSelect
            name={broadcastConfiguration.name}
            value={broadcastConfiguration.languages}
            options={multiSelectOptions}
            onChange={(e) => updateLanguage(e)}
            placeholder="Selectionner une langue ou plus"
          />
          <Button
            disabled={setButtonToDisable(broadcastConfiguration.type)}
            label="Mettre à jour"
            style={{ marginTop: '1rem', width: '100%' }}
            className="p-button-success p-mr-1"
            onClick={() => updateBroadcastConfiguration(broadcastConfiguration)}
          />
        </Card>
      ));
    }
    return <h3>Aucune configuration trouvée</h3>;
  };

  return (
    <>
      <Toast ref={toast} baseZIndex={100000} />
      <main>
        <section>
          <div className="broadcast-configurator-container">
            {renderCards()}
          </div>
        </section>
      </main>
    </>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(BroadcastConfigurationPage);
