import AbstractService from './abstractService';

class RoleMethodsProvider extends AbstractService {
  constructor() {
    super(String(process.env.REACT_APP_BASE_URL_WS).concat('/groups'));
  }

  async get(filters) {
    return new Promise((resolve, reject) => [
      super.get(filters)
        .then((json) => {
          const roles = [];
          json.forEach((role) => {
            if (role.cn) {
              roles.push(role.cn);
            }
          });
          resolve(roles);
        }).catch((error) => {
          reject(error);
        }),
    ]);
  }
}

export default RoleMethodsProvider;
