import Version from './Version';

/**
 * Class that represents an airport.
 * @since 0.9.8
 * @author METIVIER Nicolas
 */
export default class ApplicationManifest {
  constructor() {
    this.groupId = '';
    this.artifactId = '';
    this.version = '';
  }

  /**
   * Function to convert a JSON to Alert object.
   * @param {JSON} json Alert as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.groupId !== undefined) {
        if (json.artifactId !== undefined) {
          if (json.version !== undefined) {
            const manifest = new ApplicationManifest();
            manifest.groupId = json.groupId;
            manifest.artifactId = json.artifactId;
            manifest.version = Version.parse(json.version);
            return manifest;
          }
        }
      }
    }
    return null;
  }
}
