import Localization from './Localization';
import Runway from './runway';

/**
 * Class that represents an airport.
 * @since 0.0.1
 * @author METIVIER Nicolas
 */
class Airport {
  constructor() {
    this.identifier = 0;
    this.icao = '';
    this.name = '';
    this.localization = new Localization();
    this.airportType = '';
    this.runways = [];
  }

  /**
   * Function to convert a JSON to Airport object.
   * @param {JSON} json Airport as JSON object.
   */
  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.icao !== undefined) {
          if (json.name !== undefined) {
            if (json.localization !== undefined) {
              if (json.airportType !== undefined) {
                const airport = new Airport();
                airport.identifier = json.identifier;
                airport.icao = json.icao;
                airport.name = json.name;
                airport.localization = Localization.parse(json.localization);
                airport.airportType = json.airportType;
                if (json.runways != null && Array.isArray(json.runways)) {
                  for (let index = 0; index < json.runways.length; index += 1) {
                    airport.runways.push(Runway.parse(json.runways[index]));
                  }
                }
                return airport;
              }
            }
          }
        }
      }
    }
    return null;
  }
}

export default Airport;
