import Hardware from './Hardware';
import RtcGateway from './RtcGateway';
import Software from './Software';
import StapServer from './StapServer';

export default class StapSystem {
  constructor() {
    this.identifier = null;
    this.hardwares = [];
    this.softwares = [];
    this.stapServer = null;
    this.twilightSensorStatus = null;
    this.rtcGateway = null;
  }

  static compare(previous, next) {
    if (previous.name < next.name) {
      return -1;
    }
    if (previous.name > next.name) {
      return 1;
    }
    return 0;
  }

  static parse(json) {
    if (json) {
      if (json.identifier !== undefined) {
        if (json.hardwares !== undefined) {
          if (json.softwares !== undefined) {
            if (json.stapServer !== undefined) {
              if (json.twilightSensorStatus !== undefined) {
                if (json.rtcGateway !== undefined) {
                  const stapSystem = new StapSystem();
                  stapSystem.identifier = json.identifier;
                  stapSystem.twilightSensorStatus = json.identifier;
                  stapSystem.stapServer = StapServer.parse(json.stapServer);
                  if (json.hardwares != null && Array.isArray(json.hardwares)) {
                    for (let index = 0; index < json.hardwares.length; index += 1) {
                      stapSystem.hardwares.push(Hardware.parse(json.hardwares[index]));
                    }
                    stapSystem.hardwares.sort(StapSystem.compare);
                  }
                  if (json.softwares != null && Array.isArray(json.softwares)) {
                    for (let index = 0; index < json.softwares.length; index += 1) {
                      stapSystem.softwares.push(Software.parse(json.softwares[index]));
                    }
                    stapSystem.softwares.sort(StapSystem.compare);
                  }
                  stapSystem.rtcGateway = RtcGateway.parse(json.rtcGateway);
                  return stapSystem;
                }
              }
            }
          }
        }
      }
    }
    return null;
  }
}
