/* eslint-disable react/prop-types */
import React from 'react';
import { Card } from 'primereact/card';
import './styles.scss';

const VhfCard = ({ vhf }) => {
  const renderVhf = () => (
    <div className="template-container">
      <div className="name-container">
        {
          (vhf) ? (
            <div className="vhf-mode-container">
              <span className={`mode ${vhf.isReceiving ? 'activated' : ''}`}>
                <p>RX</p>
                <div />
              </span>
              <span className={`mode ${vhf.isTransmitting ? 'activated' : ''}`}>
                <p>TX</p>
                <div />
              </span>
            </div>
          ) : (
            null
          )
        }
      </div>
      {/* this.identifier = null;
    this.inputblock = 0;
    this.hardware = null;
    this.component = null;
    this.mode = null;
    this.isTransmitting = false;
    this.isReceiving = false; */}
    </div>
  );

  return (
    <Card
      // style={{ background: '#424242', color: '#ffffff' }}
      title="VHF"
    >
      { renderVhf() }
    </Card>
  );
};

export default VhfCard;
