/* eslint-disable max-len */
/* eslint-disable consistent-return */
/* eslint-disable array-callback-return */
/* eslint-disable react/prop-types */
/* eslint-disable no-unneeded-ternary */
import React, { useCallback, useEffect, useState } from 'react';
import { useSelector, connect } from 'react-redux';
import { Skeleton } from 'primereact/skeleton';
import { setError } from '../../../store/error/errorAction';
import SideBarItem from './SideBarItem';
import ApplicationManifestService from '../../../services/applicationManifestService';
import './styles.css';

const SideBar = ({ routes, setErrorInState }) => {
  const userRolesFromState = useSelector((state) => state.user.roles);
  const firstNameFromState = useSelector((state) => state.user.firstname);
  const lastNameFromState = useSelector((state) => state.user.lastname);

  const [serverVersion, setServerVersion] = useState(null);

  const getServerVersion = useCallback(async () => {
    try {
      const APPLICATION_MANIFEST_SERVICE = new ApplicationManifestService();
      const version = await APPLICATION_MANIFEST_SERVICE.get();
      setServerVersion(version.version.full);
    } catch (e) {
      setErrorInState(e.response ? e.response.status : e.status || 500);
    }
  }, [setErrorInState]);

  useEffect(() => {
    getServerVersion();
  }, [getServerVersion]);

  const renderSideBarItems = () => {
    if (userRolesFromState !== null) {
      return routes.map((route) => {
        if (route.allowedGroups) {
          for (let i = 0; i < route.allowedGroups.length; i += 1) {
            if (userRolesFromState.includes(route.allowedGroups[i])) {
              return <SideBarItem text={route.title} urlSrc={route.icon} path={route.path} badge={route.badge} />;
            }
          }
        } else {
          return <SideBarItem text={route.title} urlSrc={route.icon} path={route.path} badge={route.badge} />;
        }
      });
    }
    return <SideBarItem text="Connexion" urlSrc="/assets/images/icons/menu/login.svg" path="/login" />;
  };

  const renderUser = () => {
    if (firstNameFromState && lastNameFromState) {
      return (
        <div className="WelcomeUser">
          <img src="/assets/images/user.svg" alt="User" />
          <p>
            {firstNameFromState}
            {' '}
            {lastNameFromState}
          </p>
        </div>
      );
    }
    return null;
  };

  return (
    <nav className="sidebar-container">
      {renderUser()}
      <ul className="items-container">
        {renderSideBarItems()}
      </ul>
      <div className="version-container">
        <p>
          Version client:
          {' '}
          {process.env.REACT_APP_VERSION}
        </p>
        <p>
          Version serveur:
          {' '}
          {(serverVersion) ? serverVersion : <Skeleton />}
        </p>
      </div>
    </nav>
  );
};

// fonction du store.
const mapDispatchToProps = (dispatch) => ({
  setErrorInState: (requestStatus) => dispatch(setError(requestStatus)),
});

export default connect(null, mapDispatchToProps)(SideBar);
